    //传递需要比较的版本ID到比较页面 
    Compare() {
      // this.$router.push('/compare') 
      if(this.Selection1.length){
        this.Selection2 = this.Selection1
      }
      if(this.Selection.length){
        this.Selection2 = this.Selection;
      }
      if(this.Selection2.length){
        this.$router.push({path:'/compare',query:{
            idList:this.Selection2
        }})
      }else{
        this.$message.info("请先选择测试项目")
      }
     
    },
    // 获取单个版本报告
    getReport(val){   
      console.log(val.id)  
      let id= val.id
      this.$router.push({path:'/compare',query:{
          idList:id.toString()
      }})
      // var {href} = this.$router.resolve({
      //   path:'/compare',
      //   query:{
      //     idList:val.id
      //   }
      // });
      // window.open(href)
    },
