      if(!this.divList[0].setSelectInfo.selfProject&&!this.divList[0].setSelectInfo.comProject){
        this.getRecentProject()
        return;
      }
      this.divList[index].setSelectInfo.selfVersionId='';
      if(this.divList[index].setSelectInfo.selfProject=="去吧皮卡丘"){
        this.divList[index].setSelectInfo.comProject="少年三国志"
        this.divList[index].setSelectInfo.comVersionId='';
      }else if(this.divList[index].setSelectInfo.selfProject=="疯狂原始人"){
