        this.divList[index].setSelectInfo.comVersionId='';
      }else if(this.divList[index].setSelectInfo.selfProject=="从零开始的异世界生活-无限"){
        this.divList[index].setSelectInfo.comProject="崩坏3"
        this.divList[index].setSelectInfo.comVersionId='';
      }else if(this.divList[index].setSelectInfo.selfProject=="深蓝战舰"){
        this.divList[index].setSelectInfo.comProject="放置奇兵"
        this.divList[index].setSelectInfo.comVersionId='';
      }
      // console.log(this.selfProject)
      // this.optionsVersion1=[]
      // this.selfVersionId=""
      for(let i=0;i<this.options1.length;i++){
        if(this.divList[index].setSelectInfo.selfProject==this.options1[i].game_name){
          if(this.divList[index].setSelectInfo.platform=="ios"){   //平台对应版本绑定
            this.optionsVersion1[index] = this.options1[i].ios_version_list
          }else if(this.divList[index].setSelectInfo.platform=="android"){
            this.optionsVersion1[index] = this.options1[i].android_version_list
          }
          // this.optionsVersion1[index] = this.options1[i].version_list
          break;
        }
