      this.setData.data=JSON.stringify(this.divList) //get不能直接传列表，需先转换成json字符串
      // this.setData.data = Object.assign({},this.divList)
      // const {data:res} = await this.$http.post("get_select_list/",this.setData) 
      const {data:res} = await this.$http.get("get_select_list",{params:this.setData});
