          if(this.divList[index].setSelectInfo.platform=="ios"){   //平台对应版本绑定
            this.optionsVersion2[index] = this.options2[i].ios_version_list
          }else if(this.divList[index].setSelectInfo.platform=="android"){
            this.optionsVersion2[index] = this.options2[i].android_version_list
          }
          // this.optionsVersion2[index] = this.options2[i].version_list
