      // let setSelectInfo=[{
      //   selfProject: this.selfProject,
      //   selfVersionId: this.selfVersionId,
      //   comProject:this.comProject,
      //   comVersionId:this.comVersionId,
      //   fl_list:this.fl_list,
      //   platform:this.platform,
      // }]
     this.setData.data=JSON.stringify(this.divList)
      const {data:res} = await this.$http.get("get_select_list",{params:this.setData});
