import Vue from 'vue'
import VueRouter from 'vue-router'
import client_report from '../components/clientReport/client_report.vue'
import CompareReport from '../components/clientReport/CompareReport.vue'
import home from '../components/home.vue'
import home2 from '../components/home2.vue'
import login from '../components/login.vue'

import Unity_report from '../components/UnityData/Unity_report.vue'
import Unity_detail from '../components/UnityData/Unity_detail.vue'
import Unity_Summary from '../components/UnityData/Summary.vue'
import Unity_CPU_function from '../components/UnityData/CPU_function.vue'
import Unity_CPU_performace from '../components/UnityData/CPU_performace.vue'
import Unity_Hard_ele from '../components/UnityData/Hard_ele.vue'
import Unity_Hard_GPU from '../components/UnityData/Hard_GPU.vue'
import Unity_Memory_consume from '../components/UnityData/Memory_consume.vue'
import Unity_Memory_resource from '../components/UnityData/Memory_resource.vue'
import Unity_Rendering_summary from '../components/UnityData/Rendering_summary.vue'
import Unity_Rendering_Batch from '../components/UnityData/Rendering_Batch.vue'

import AdReport from '../components/AdaptiveReport/AdSumReport.vue'
import Ad_detail from '../components/AdaptiveReport/Ada_detail.vue'
import Ad_Analyse from '../components/AdaptiveReport/AdAnalyse.vue'
import Ad_Summary from '../components/AdaptiveReport/AdSummary.vue'
import Ad_Perfermance from '../components/AdaptiveReport/AdPerfermance.vue'
import Ad_Problem from '../components/AdaptiveReport/AdProblem.vue'
import Ad_Devices from '../components/AdaptiveReport/AdDevices.vue'
import AdDevice_detail from '../components/AdaptiveReport/AdDevice_detail.vue'
import upload from '../components/upload.vue'

import SaReport from '../components/SafeReport/SumReport.vue'
import Sadetail from '../components/SafeReport/Sadetail.vue'
import StReport from '../components/StressReport/SumReport.vue'
import St_detail from '../components/StressReport/St_detail.vue'

//设备借用
import Devices from "../components/Devices_borrow/Devices/Devices.vue"
import Devices_ios from "../components/Devices_borrow/Devices/Devices_ios.vue"
import Devices_Android from "../components/Devices_borrow/Devices/Devices_Android.vue"
import Admin from "../components/Devices_borrow/admin/admin.vue"
import user from "../components/Devices_borrow/user.vue"
import userhome from "../components/Devices_borrow/Devices/userhome.vue"
import adminDevices from "../components/Devices_borrow/Devices/adminDevices.vue"
import adminDevices_ios from "../components/Devices_borrow/Devices/adminDevices_ios.vue"
import adminDevices_Android from "../components/Devices_borrow/Devices/adminDevices_Android.vue"
import addnew from "../components/Devices_borrow/admin/addnew.vue"
import checked from "../components/Devices_borrow/admin/checked.vue"



import Vuecookies from 'vue-cookies'

Vue.use(VueRouter)
// 兼容IE报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location){
  return originalPush.call(this,location).catch(err=>err);
}

  const routes = [
  {
      path: '/',
      redirect: '/login'
  },
  {
    path: '/login',
    component: login
  },
  {
    path: '/home',
    component: home,
    redirect: '/client_report',
    children: [
        { path: '/client_report', component: client_report },
        { path: '/Unity_report', component: Unity_report },
        { path: '/AdReport', component: AdReport },
        { path: '/SaReport', component: SaReport },
        { path: '/StReport', component: StReport },
   
    ]
  },
  {
    path: '/home2',
    component: home2,
    redirect: '/AdReport2',
    children: [
        //性能数据报告
        { path: '/client_report2', component: client_report },
        { path: '/Compare', component: CompareReport},
        //unity数据报告
        { path: '/Unity_report2', component: Unity_report },
        {
          path:'/Unity_detail',
          component: Unity_detail,
          redirect: '/Unity_Summary',
          children: [
              { path: '/Unity_Summary', component: Unity_Summary },
              { path: '/Unity_CPU_function', component: Unity_CPU_function },
              { path: '/Unity_CPU_performace', component: Unity_CPU_performace },
              { path: '/Unity_Hard_ele', component: Unity_Hard_ele },
              { path: '/Unity_Hard_GPU', component: Unity_Hard_GPU },
              { path: '/Unity_Memory_consume', component: Unity_Memory_consume },
              { path: '/Unity_Memory_resource', component: Unity_Memory_resource },
              { path: '/Unity_Rendering_summary', component: Unity_Rendering_summary },
              { path: '/Unity_Rendering_Batch', component: Unity_Rendering_Batch },
          ]
        },
        //适配报告
        { path: '/AdReport2', component: AdReport },
        {
          path: '/Ad_detail',
          component: Ad_detail,
          redirect: '/Ad_Summary',
          children: [
              { path: '/Ad_Summary', component: Ad_Summary },
              { path: '/Ad_Analyse', component: Ad_Analyse },
              { path: '/Ad_Perfermance', component: Ad_Perfermance },
              { path: '/Ad_Problem', component: Ad_Problem },
              { path: '/Ad_Devices', component: Ad_Devices },
              { path: '/AdDevice_detail', component: AdDevice_detail },
          ]
        },

        //安全性报告
        { path: '/SaReport2', component: SaReport },
        { path: '/Sadetail',component: Sadetail},
        //压力报告
        { path: '/StReport2', component: StReport },
        { path: '/St_detail', component: St_detail },
        //设备借用
        {
          path: '/Devices',
          component: userhome,
          redirect: '/all_devices',
          children: [
              { path: '/all_devices', component: Devices },
              { path: '/user', component: user },
              { path: '/devices_ios', component: Devices_ios },
              { path: '/devices_android', component: Devices_Android }
          ]
        },
        {
          path: '/admin',
          name:'admin',
          component: Admin,
          redirect: '/admindevices',
          children: [
              { path: '/admindevices', component: adminDevices },
              { path: '/admindevices_ios', component: adminDevices_ios },
              { path: '/admindevices_android', component: adminDevices_Android },
              { path: '/checked', component: checked },
              { path: '/addnew', component: addnew }
          ]
        },

    ]
  },


  // {
  //   path: '/Compare',
  //   component: CompareReport
  // },

  {
    path: '/upload',
    component: upload
  },


]

const router = new VueRouter({
  mode: 'history',
  routes
})

//挂载路由导航守卫
router.beforeEach((to, from, next) => {
  //     //to 将要访问的路劲
  //     //from 代表从哪个路径跳转出来
  //     //next 是一个行数，表示放行
  //     //  next（）放行   next('/login') 强制跳转
      if (to.path == '/login') return next();
  //     //获取token值
      const tokenStr = Vuecookies.get('token');
      //const sessionid = Vuecookies.get("SESSIONID");
      //console.log(tokenStr)
      // if (!tokenStr) return next('/login') //正式环境修改
      // if (!sessionid) return next('/login')
      if (!tokenStr) return next('/login')
      next()
  })
  

export default router
