// import 'react-app-polyfill/ie9';
// import'react-app-polyfill/stable';
//import 'jquery'
//import "babel-polyfill"
import Promise from 'es6-promise'
import Vue from 'vue'
// 导入全局样式表
import './assets/fonts/iconfont.css'
import './assets/fonts/iconfont.js'
import './assets/css/global.css'
import App from './App.vue'
import './plugins/element.js'
import router from './router'
import axios from 'axios'
import echarts from "echarts"
import Vuecookies from 'vue-cookies'
// import { MessageBox } from 'element-ui'
import apiconfig from '../api.config'
import Vuejsonp from 'vue-jsonp'
import VueDirectiveImagePreviewer from 'vue-directive-image-previewer'
import 'vue-directive-image-previewer/dist/assets/style.css'
Vue.use(VueDirectiveImagePreviewer)

Vue.use(Vuejsonp)

Promise.polyfill()
Vue.config.productionTip = false

//配置请求的根路径
//axios.defaults.baseURL = 'http://10.0.10.246:8091' 
axios.defaults.baseURL = apiconfig.baseUrl;
axios.defaults.withCredentials = false;
//axios.defaults.withCredentials=true;
// 请求拦截器，使拥有获取数据的权限,token验证
axios.interceptors.request.use(config => {
    //console.log(config);
    config.headers.Authorization = Vuecookies.get('token');
    //config.headers.Authorization = Vuecookies.get('SESSIONID');
    return config
})

// 拦截器 验证token是否过期，（2）兼容IE
axios.interceptors.response.use(
    response=>{
        // IE 8-9
        if(response.data == null &&response.config.responseType ==='json'&&response.request.responseText !=null){
            try{
                response.data = JSON.parse(response.request.responseText);
            }catch(e){
                // ignored
            }
        }
        console.log(response.data)
        if(response.data.meta.status===500){
            window.sessionStorage.clear();
            // MessageBox.alert('登录信息超时，尝试重新登录中...')
            // 重定向到登录页
            setTimeout(
                "parent.location.href='/Login'",2000
            )
            
        }
        return response
    }
)

// 页面共享session值
var constant={
    SESSION_FLAG:'',
    SESSION_ID:'',
}
// 1.  触发标志位改变事件
window.localStorage.setItem(constant.SESSION_FLAG, Date.now().toString())
window.addEventListener("storage", function(event){
    if(!event.newValue){
        return;
    }
    // 2. 监听标志位改变事件
    if(event.key === constant.SESSION_FLAG){
    // 3. 触发传递信息的改变事件
        localStorage.setItem("storeSessionData", sessionStorage.getItem(constant.SESSION_ID))
        localStorage.removeItem("storeSessionData")
    } else if (event.key === 'storeSessionData') { // 4. 监听 传递信息 的改变事件
        sessionStorage.setItem(constant.SESSION_ID, event.newValue)
    }
})

//router点击跳转回到页面顶部
router.afterEach(()=>{
    window.scrollTo(0,0)
})


Vue.prototype.$cookies = Vuecookies
Vue.prototype.$http = axios
Vue.prototype.$echarts = echarts

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
