<template>
  <div>
   <div style="height:auto;min-height:1000px">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Ad_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>性能分析</el-breadcrumb-item>
      </el-breadcrumb> 
      <el-tabs v-model="activeNames"  @tab-click="clickTabs" stretch style="justify-content:center;margin-top:50px">
        <el-tab-pane  v-for="(item,index) in tabs" :key="index"  :name="String(index)" style="padding:40px">
          <span slot="label" style="font-size:20px" :class="item.icon">{{item.title}}</span>
          <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700;color:grey">
             <i class="el-icon-s-unfold" >
              </i> {{item.title}} </div>
              <div style="height:350px;width:auto;margin-top:20px" :id="item.title" ></div>
          </div>
          <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700;color:grey">
             <i class="iconfont icon-keliuqushi " >
              </i> {{item.content1}} </div>    
           <div style="height:350px;width:auto;margin-top:20px" :id="item.title+item.content1" ></div>
          </div>
          <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700;color:grey">
             <i class="iconfont icon-jixing   " >
              </i> {{item.content2}} </div>    
              <el-input style="width:200px;margin-top:50px;float:right" v-model="assetTypeSearch" size="middle" placeholder="输入设备名" @input="Serch"></el-input>
              <el-table @sort-change="changeSort" :data="pageInfo.currentData" style="width: 100%" max-height="500" highlight-current-row @row-click="open_device_detail">
                  <el-table-column type="index" label="序号" align="center"  width="250"></el-table-column>
                  <el-table-column prop="DeviceName" label="设备型号" sortable align="center" width="500"></el-table-column>
                  <el-table-column :prop="Devices_prop" :label="item.title" sortable align="center" >
                      <template slot-scope="scope">
                        <span v-if="index==0 && scope.row.perf.avg_cpu<getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                        <span v-if="index==0 && scope.row.perf.avg_cpu>getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                        <span v-if="index==1 && scope.row.perf.avg_memory<getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                        <span v-if="index==1 && scope.row.perf.avg_memory>getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                        <span v-if="index==2 && scope.row.perf.avg_data<getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                        <span v-if="index==2 && scope.row.perf.avg_data>getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                        <span v-if="index==3 && scope.row.perf.avg_fps<getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                        <span v-if="index==3 && scope.row.perf.avg_fps>getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                        <span v-if="index==0">{{scope.row.perf.avg_cpu}}%</span>
                        <span v-if="index==1">{{scope.row.perf.avg_memory}} MB</span>
                        <span v-if="index==2">{{scope.row.perf.avg_data}} kb</span>
                        <span v-if="index==3">{{scope.row.perf.avg_fps}}</span>
                      </template>
                  </el-table-column>
              </el-table>
              <!-- 页码区-->
              <el-pagination  @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="DevicesData.length"></el-pagination>
              <p style="color:grey;font-size:10px;float:right">注：<span  class="iconfont icon-gao" style="color:green" ></span>性能表现优于历史平均；<span style="color:red" class="iconfont icon-di" ></span>性能表现差于历史平均</p>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>     
</template>
<script>
export default {
  data(){
return {  
      pageInfo: {
        Devices_prop:'',
        pagenum: 1,
        pagesize: 10, 
        currentData:[],
      },
      activeNames:"0", 
      historyVersion:[],
      tabs:[
        {
        icon:'iconfont icon-cpu ',
        title:'CPU占用率',
        content1:'历史版本平均数据走势',
        content2:'性能数据排行',
        data:{devices:[0,5,7,8,9],historyAverage:[10.00,11.32,15.26,20.14,18.11] }
        },
        {
        icon:'iconfont icon-banben ',
        title:'内存占用',
        content1:'历史版本平均数据走势',
        content2:'性能数据排行',
        data:{devices:[0,5,7,8,9],historyAverage:[10.00,11.32,15.26,20.14,18.11] }
        },
        {icon:'iconfont icon-dbfx ',
        title:'流量',
        content1:'历史版本平均数据走势',
        content2:'性能数据排行',
        data:{devices:[0,5,7,8,9],historyAverage:[10.00,11.32,15.26,20.14,18.11] }
        },
        {icon:'iconfont icon-xingnengtongji ',
        title:'帧数率',
        content1:'历史版本平均数据走势',
        content2:'性能数据排行',
        data:{devices:[0,5,7,8,9],historyAverage:[10.00,11.32,15.26,20.14,18.11] }
        }
        
      ],
      arrVersion:["v1.0.0","v1.1.0","v1.2.0","v1.3.0","v1.4.0"],
      DevicesData:[],
      assetTypeSearch:"",
    }    
  },
  created(){
    this.getProblemInfo();
    this.getDavicesData();
    this.Devices_prop="perf.avg_cpu"
  },
  computed:{
  },
  methods:{
    //求平均值
    getAverage(arr){
      let sum=0
      for(let i=0;i<arr.length;i++){
        sum+=arr[i]
      }
      return sum/arr.length
    },
    //搜索
    Serch(){
      let serchData = this.DevicesData.filter(data=>
        !this.assetTypeSearch || data.DeviceName.toLowerCase().includes(this.assetTypeSearch.toLowerCase())
      )
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = serchData.slice(begin,end); 
    },
    //排序
    changeSort(val){
      // console.log(val)
      let key1 =(val.prop).split('.')[0]
      let key2 =(val.prop).split('.')[1]
      let sortData = this.DevicesData

      if(val.order==="descending"){
        sortData.sort(this.sortkey(key1,key2,true))
      }
      if(val.order==="ascending"){
        sortData.sort(this.sortkey(key1,key2,false))
      }
      if(val.order==null){
        //重新请求数据，回复默认
        this.getProblemInfo()
      }
      this.DevicesData=sortData
      this.setCurrentPageData();

    },
    compare(x,y){
      return x<y?1:-1
    },
    //升序降序
    sortkey(key1,key2,order){
      if(order){
        return (val1,val2)=>{
          return val1[key1][key2]<val2[key1][key2]?1:-1
        }
      }else{
        return (val1,val2)=>{
          return val1[key1][key2]>val2[key1][key2]?1:-1
        }
      }
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.DevicesData.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },
    // forceUpdate(){
    //   this.$forceUpdate();
    // },
    async getProblemInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('performance_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.arrVersion = res.data.dict.res.History.version
      this.tabs[0].data.devices=res.data.dict.res.cpu.DevicesNum
      this.tabs[0].data.historyAverage=res.data.dict.res.History.cpu
      this.tabs[1].data.devices=res.data.dict.res.memory.DevicesNum
      this.tabs[1].data.historyAverage=res.data.dict.res.History.memory
      this.tabs[2].data.devices=res.data.dict.res.data.DevicesNum
      this.tabs[2].data.historyAverage=res.data.dict.res.History.data
      this.tabs[3].data.devices=res.data.dict.res.fps.DevicesNum
      this.tabs[3].data.historyAverage=res.data.dict.res.History.fps

      setTimeout(()=>{
        this.DoEchart(this.tabs[0].title,'');
        this.DoEchart(this.tabs[0].title,this.tabs[0].content1);
      },200)

    },
    async getDavicesData(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('devices_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.DevicesData=res.data.dict.res
      this.setCurrentPageData();

    },

    clickTabs(a){
     // console.log(a.index)
      if(a.index==0){
        this.Devices_prop="perf.avg_cpu"
      }
      if(a.index==1){
        this.Devices_prop="perf.avg_memory"
      }
      if(a.index==2){
        this.Devices_prop="perf.avg_data"
      }
      if(a.index==3){
        this.Devices_prop="perf.avg_fps"
      }
      
      setTimeout(()=>{
      this.DoEchart(this.tabs[Number(a.index)].title,'');
      this.DoEchart(this.tabs[Number(a.index)].title,this.tabs[Number(a.index)].content1);
      },200)
    },
    DoEchart(key1,key2){
      //console.log(key1+key2)
      var ElementID = document.getElementById(key1+key2)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let _color='#54589b'
      let value=null
      let arrCpu=["0-10%", "10%-20%", "20%-30%" , "30%-50%" ,"50%以上",""]
      let arrMemory=[ "0-100MB", "100MB-200MB", "200MB-500MB" ,"500MB-1000MB" ,"1000MB以上", "" ]
      let arrNetwork=[ "0-100KB", "100KB-200KB", "200KB-500KB" ,"500KB-1000KB" ,"1000KB以上","" ]
      let arrFps=["0－10", "10-20", "20-30" ,"30-50" ,"50以上", "" ]
      let Xaxis=null
      let legentTitle=null
      let type = "line"
      if(key1+key2==this.tabs[0].title){
        legentTitle = "台数"
        Xaxis = arrCpu
        value = this.tabs[0].data.devices
        type = "bar"
      }else if(key1+key2==this.tabs[1].title){
        legentTitle = "台数"
        Xaxis = arrMemory
        value = this.tabs[1].data.devices
        type = "bar"
        _color='#67b5a6'
      }else if(key1+key2==this.tabs[2].title){
        legentTitle = "台数"
        Xaxis = arrNetwork
        value = this.tabs[2].data.devices
        type = "bar"
        _color='#6a85c9'
      }else if(key1+key2==this.tabs[3].title){
        legentTitle = "台数"
        Xaxis = arrFps
        value = this.tabs[3].data.devices
        type = "bar"
        _color='#6aaec9'
      }else if(key1+key2==this.tabs[0].title+this.tabs[0].content1){
        Xaxis = this.arrVersion
        legentTitle = key1
        value = this.tabs[0].data.historyAverage
      }else if(key1+key2==this.tabs[1].title+this.tabs[1].content1){
        Xaxis = this.arrVersion
        legentTitle = key1
        value = this.tabs[1].data.historyAverage
        _color='#67b5a6'
      }else if(key1+key2==this.tabs[2].title+this.tabs[2].content1){
        Xaxis = this.arrVersion
        legentTitle = key1
        value = this.tabs[2].data.historyAverage
        _color='#6a85c9'
      }else if(key1+key2==this.tabs[3].title+this.tabs[3].content1){
        Xaxis = this.arrVersion
        legentTitle = key1
        value = this.tabs[3].data.historyAverage
        _color='#6aaec9'
      }
      

      let seriesData={
          name:legentTitle ,
          type: type,
          data:value ,
          barWidth:40,
          markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
          },
          markLine:{
            data:[{type:"average",name:'平均值'}]
          }
      }

      let option = {
        tooltip: {
            trigger: 'axis',
        },
        color:_color,
        legend: {
            data: ['台数',key1]
        },
        toolbox: {
            show: true,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                boundaryGap:false,
                data:Xaxis
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    },
    open_device_detail(val){
      this.$router.push({path:'/AdDevice_detail',query:{
          Deviceid:val.id,
      }})
    }
  }
}
</script>


<style  lang="less" scoped>
  .el-main {
    background-color:snow;
    width: 100%;
  }
  .el-aside {
	background-color: #333744;
  }
  .el-menu {
      border-right: none;
  }
  .imgHeader {
			width: 170px;
			margin-left: -18px;
			margin-top: 0px;
      float: left;
  }
  .toggle-button {
	background-color: #4a5064;
	font-size: 10px;
	line-height: 20px;
	text-align: center;
	color: white;
	cursor: pointer;
}
</style>