<template>
  <div>
    <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Ad_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>设备列表</el-breadcrumb-item>
      </el-breadcrumb> 
      <div style="height:auto;min-height:1000px;margin-top:-50px">
        <el-input style="width:200px;margin-top:50px;float:right" v-model="assetTypeSearch" size="middle" placeholder="输入设备名" @input="Serch"></el-input>
        <el-table @sort-change="changeSort" :data="pageInfo.currentData" style="width: 100%"  >
            <el-table-column type="index" label="序号" align="center"  ></el-table-column>
            <el-table-column prop="DeviceName" label="设备型号" sortable align="center" ></el-table-column>
            <el-table-column prop="Result" label="测试结果" sortable align="center" >
              <template slot-scope="scope">
                <div v-if="scope.row.Result==true" style="margin:0px auto;height:25px;width:50px;text-align:center;color:white;border:2px solid rgb(80,177,80);border-radius:15px;background:rgb(80,177,80)">通过</div>
                <div v-else  style="margin:0px auto;height:25px;width:50px;text-align:center;color:white;border:2px solid rgb(217,66,60);border-radius:15px;background:rgb(217,66,60)">失败</div>
              </template>
            </el-table-column>
            <el-table-column prop="Memory" label="内存" sortable align="center"></el-table-column>
            <el-table-column prop="Screen" label="分辨率" sortable align="center" ></el-table-column>
            <!-- <el-table-column prop="Cpu" label="CPU型号" sortable align="center" ></el-table-column> -->
            <el-table-column prop="SystemVersion" label="系统版本" sortable align="center" ></el-table-column>
            <el-table-column  label="报告详情"  align="center" >
                <template slot-scope="scope">
                  <el-button type="text" style="font-size:12px;" @click="open_device_detail(scope.row.id)">查看详情</el-button>
                </template>
            </el-table-column>
            <!-- <el-table-column prop="data" label="i" sortable align="center" >
                <template slot-scope="scope">
                  <span v-if="index!=3 && scope.row.data<getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                  <span v-if="index!=3 && scope.row.data>getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                  <span v-if="index==3 && scope.row.data<getAverage(item.data.historyAverage)" style="color:red" class="iconfont icon-di" ></span>
                  <span v-if="index==3 && scope.row.data>getAverage(item.data.historyAverage)" style="color:green" class="iconfont icon-gao" ></span>
                  <span >{{scope.row.data}}</span>
                </template>
            </el-table-column> -->
        </el-table>
        <!-- 页码区-->
        <el-pagination  @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="testData.length"></el-pagination>
      </div>
  </div>     
</template>
<script>
export default {
  data(){
return {  
      pageInfo: {
        pagenum: 1,
        pagesize: 10, 
        currentData:[],
      },
      assetTypeSearch:null,
      testData:[]
        
      }
  },
  created(){
    this.getdevicesInfo();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    //搜索
    Serch(){
      let serchData = this.testData.filter(data=>
        !this.assetTypeSearch || data.DeviceName.toLowerCase().includes(this.assetTypeSearch.toLowerCase())
      )
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = serchData.slice(begin,end); 
    },
    //排序
    changeSort(val){
      let sortData = this.testData
      if(val.order==="descending"){
        sortData.sort(this.sortkey(val.prop,true))
      }
      if(val.order==="ascending"){
        sortData.sort(this.sortkey(val.prop,false))
      }
      if(val.order==null){
        //重新请求数据，恢复默认
      }
      console.log(sortData)
      this.testData=sortData
      this.setCurrentPageData();

    },
    //升序降序
    sortkey(key,order){
      if(order){
        return (val1,val2)=>{
          return val2[key]>val1[key]?1:-1
        }
      }else{
        return (val1,val2)=>{
          return val2[key]<val1[key]?1:-1
        }
      }
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.testData.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    async getdevicesInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('devices_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.testData = res.data.dict.res

      this.setCurrentPageData();
    },

    open_device_detail(key){
      //console.log(key)
      this.$router.push({path:'/AdDevice_detail',query:{
          Deviceid:key,
      }})
    },

  }
}
</script>


