<template>
  <div style="height:auto;min-height:1000px">
    <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>电量和温度</el-breadcrumb-item>
      </el-breadcrumb>
      <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> 电量信息</div>
              <div style="height:350px;width:auto;margin-top:20px" id="ele"></div>
      </div>
      <div style="width:100%;float:left;margin-bottom:60px">
        <div style="font-weight:700">
          <i class="el-icon-s-unfold" >
          </i> 温度信息</div>    
        <div style="height:350px;width:auto;margin-top:20px" id="temp"></div>
      </div>
  </div>  
</template>
<script>
export default {
  data(){
return {  
      isCollapse: false,
      hardData:{
        ele:[],
        temp:[],
        time:[]
      },
      Xaxis:[],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
    }
  },
  created(){
    this.getdata();
  },
  computed:{
  },
  methods:{
    async getdata(){
       const value={
        id:window.localStorage.getItem("id"),
        type:4,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.hardData.ele = res.data.dict.bat;
      this.hardData.temp = res.data.dict.temp;
      this.hardData.time = res.data.dict.time;

      let xDemo = this.hardData.time;
      for(let i=0;i<xDemo.length;i++){
        let a =xDemo[i]-xDemo[0]
        let min = parseInt(a/(1000*60))
        if(min<10){
          min = '0'+min
        }
        let second=parseInt((a%(1000*60))/1000)
        if(second<10){
          second = '0'+second
        }
        let x = min+":"+second
        this.Xaxis.push(x)
      } 
      setTimeout(()=>{
        this.DoEchart("ele");
        this.DoEchart("temp")
      },500)
    },
    DoEchart(id){
      var ElementID = document.getElementById(id)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var seriesData = {}
      var Etitle=""
      if(id=="ele"){
        Etitle = "剩余电量(百分比)"
        seriesData = {
        name: "Battery",
        type: 'line',
        data: this.hardData.ele,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
         },
        }
      }
      if(id=="temp"){
        Etitle="温度(℃)"
        seriesData = {
        name: "Temp",
        type: 'line',
        data: this.hardData.temp,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
         },
        }
      }

      let option = {
        title: {
            text: Etitle,
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
        },
        legend: {
            data: ['Temp', 'Battery']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.Xaxis
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    }
  }
}
</script>


