<template>
      <div style="height:auto;min-height:1000px"> 
      <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>图形概况</el-breadcrumb-item>
      </el-breadcrumb>        
        <!-- <span>
          <input class="input-file" id="csvFileInput" type="file" @change="readCSVfile" accept=".csv" />
          <button type="button"  name="seach" @click="csvInput">input</button>
        </span> -->
        <div style="margin-bottom:40px;width:100%;float:left;border-bottom:1px solid #eee;margin-top:50px">
          <div style="height:auto;width:100%;margin-left:100px;">
          <!-- <div style="display:flex;justify-content:space-between;margin:20px 0 30px 60px;width:90%;float:auto"> -->
            <div v-for="(item,index) in MainCard" :key="index"  class="dataTab" >
              <div style="font-weight:700;text-align:center">{{item.title}}
              </div>
              <div style="margin-top:20px;font-size:40px;font-weight:700;text-align:center">{{item.data}}</div>
            </div>
          </div> 
        </div> 
        <div style="width:100%;float:left;margin-bottom:60px">
          <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> SetPassCalls / DrawCalls</div>
          <div style="height:350px;width:auto;margin-top:20px" id="setPassCall"></div>
        </div>
        <div style="width:100%;float:left">
          <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> Vertices / Triangles</div>
          <div style="height:350px;width:auto;margin-top:20px" id="Vertices"></div>
        </div>
        <div style="width:100%;float:left">
          <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> VBOCount</div>
          <div style="height:350px;width:auto;margin-top:20px" id="VBOCount"></div>
        </div>
        <div style="width:100%;float:left">
          <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> VBOSize</div>
          <div style="height:350px;width:auto;margin-top:20px" id="VBOSize"></div>
        </div>
      </div>    
</template>
<script>
import XLSX from 'xlsx'
export default {
  data(){
    return {
      // 数据面板
      MainCard:[{
          title:"SetPassCalls峰值(次)",
          data:0,
        },{
          title:"SetPassCalls帧均值(次)",
          data:0,
        },{
          title:"DrawCalls峰值(次)",//纹理资源
          data:0,
        },{
          title:"DrawCalls帧均值(次)",
          data:0,
        },{
          title:"Triangles峰值(面)",
          data:0,
        },{
          title:"Triangles帧均值(面)",
          data:0,
        },{
          title:"Vertices峰值(点)",
          data:0,
        },{
          title:"Vertices帧均值(点)",
          data:0,
        }
      ],
      datamodel:{
        frameIdx:[],
        setPassCalls:[],
        drawCalls:[],
        triangles:[],
        vertices:[]
      },
      AllData:{
  
        dynamicBatchedDrawCalls:[],
        dynamicBatchedTriangles:[],
        dynamicBatchedVertices:[],
        dynamicBatches:[],

        staticBatchedDrawCalls:[],
        staticBatchedTriangles:[],
        staticBatchedVertices:[],
        staticBatches:[],

        setPassCalls:[],
        totalbatches:[],
        drawCalls:[],
        triangles:[],
        vboTotal:[],
        vboTotalBytes:[],
        vertices:[],

        frameIdx:[],

      },
      isCollapse: false,
      testData:{
        SetPassCallsMax:0,
        SetPassCallsAvg:0,
        DrawCallsMax:0,
        DrawCallsAvg:0,
        TrianglesMax:0,
        TrianglesAvg:0,
        VerticesMax:0,
        VerticesAvg:0
      },
      testData1:[],
      color_Object:{
        green:'green',
        red:'red',
      },
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素
      testdata3:'',
    }
  },
  created(){
    this.getRendering();
    //this.HandleData()
    // setTimeout(()=>{
    //   this.DoEchart('setPassCall');
    //   this.DoEchart('Vertices');
    // },100)
    //this.readCSVfile();
  },
  methods:{
    handCSVData(data){
      for(let i=0;i<data.length;i++){
        this.datamodel.frameIdx = this.datamodel.frameIdx.concat(data[i].frameIdx)
        this.datamodel.setPassCalls = this.datamodel.setPassCalls.concat(data[i].setPassCalls)
        this.datamodel.drawCalls = this.datamodel.drawCalls.concat(data[i].drawCalls)
        this.datamodel.triangles = this.datamodel.triangles.concat(data[i].triangles)
        this.datamodel.vertices = this.datamodel.vertices.concat(data[i].vertices)
      }
      //console.log(this.datamodel);
      this.setData();
      window.localStorage.setItem("Rendering",JSON.stringify(this.datamodel));
      window.localStorage.setItem("Batch",JSON.stringify(this.testData));
      setTimeout(()=>{
        this.DoEchart('setPassCall','次数',this.datamodel);
        this.DoEchart('Vertices','数量(K)',this.datamodel);
      },100)
    },
    readCSVfile(){
      const that = this
      // 获取文件对象
      var f = event.currentTarget.files[0]
      console.log(f);
      //var f = "10.0.10.246/ProfilerTestData/rendering.csv"
      var reader = new FileReader()
       // 重写FileReader上的readAsBinaryString方法
      FileReader.prototype.readAsBinaryString = function (f) {
        var binary = ''
        var wb // 读取完成的数据
        var outdata // 需要的数据
        var reader = new FileReader()
        reader.onload = function () {
          // 读取成Uint8Array，再转换为Unicode编码（Unicode占两个字节）
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          // 接下来就是csv
          wb = XLSX.read(binary, {
            type: 'binary'
          })
          // outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])
          // 自定义方法向父组件传递数据
          //console.log(JSON.stringify(outdata))
          that.csvdata = JSON.stringify(outdata)
          that.handCSVData(outdata);
        }
        reader.readAsArrayBuffer(f)
      }
      reader.readAsBinaryString(f)    
    },
    csvInput(){
        $("#csvFileInput").val("");
        $("#csvFileInput").click();
    },    
    HandleData(){
      this.testData1.length=this.MainCard.length
      this.MainCard[0].data=Math.max.apply(null,this.AllData.setPassCalls)  
      this.MainCard[1].data= ((eval(this.AllData.setPassCalls.join("+"))-Math.max.apply(null,this.AllData.setPassCalls)-Math.min.apply(null,this.AllData.setPassCalls))/(this.AllData.setPassCalls.length-2)).toFixed(2)
      this.MainCard[2].data=Math.max.apply(null,this.AllData.drawCalls)  
      this.MainCard[3].data= ((eval(this.AllData.drawCalls.join("+"))-Math.max.apply(null,this.AllData.drawCalls)-Math.min.apply(null,this.AllData.drawCalls))/(this.AllData.drawCalls.length-2)).toFixed(2)
      this.MainCard[4].data=Math.max.apply(null,this.AllData.triangles)*1000 
      this.MainCard[5].data= ((eval(this.AllData.triangles.join("+"))-Math.max.apply(null,this.AllData.triangles)-Math.min.apply(null,this.AllData.triangles))/(this.AllData.triangles.length-2)).toFixed(2)*1000
      this.MainCard[6].data=Math.max.apply(null,this.AllData.vertices)*1000  
      this.MainCard[7].data= ((eval(this.AllData.vertices.join("+"))-Math.max.apply(null,this.AllData.vertices)-Math.min.apply(null,this.AllData.vertices))/(this.AllData.vertices.length-2)).toFixed(2)*1000
      setTimeout(()=>{
        this.DoEchart('setPassCall');
        this.DoEchart('Vertices');
        this.DoEchart('VBOCount');
        this.DoEchart('VBOSize');
      },100)

    },
    // 初始化渲染数据：
    async getRendering(){
      const value={
        id:window.localStorage.getItem("id"),
        type:0,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.AllData.dynamicBatchedDrawCalls=res.data.dict.rendering.dynamicBatchedDrawCalls;
      this.AllData.dynamicBatchedTriangles=res.data.dict.rendering.dynamicBatchedTriangles;
      this.AllData.dynamicBatchedVertices=res.data.dict.rendering.dynamicBatchedVertices;
      this.AllData.dynamicBatches=res.data.dict.rendering.dynamicBatches;
      this.AllData.staticBatchedDrawCalls=res.data.dict.rendering.staticBatchedDrawCalls;
      this.AllData.staticBatchedTriangles=res.data.dict.rendering.staticBatchedTriangles;
      this.AllData.staticBatchedVertices=res.data.dict.rendering.staticBatchedVertices;
      this.AllData.staticBatches=res.data.dict.rendering.staticBatches;

      this.AllData.setPassCalls=res.data.dict.rendering.setPassCalls;
      this.AllData.totalbatches=res.data.dict.rendering.totalbatches;
      this.AllData.drawCalls=res.data.dict.rendering.drawCalls;
      this.AllData.triangles=res.data.dict.rendering.triangles;
      this.AllData.vboTotal=res.data.dict.rendering.vboTotal;
      this.AllData.vboTotalBytes=res.data.dict.rendering.vboTotalBytes;
      this.AllData.vertices=res.data.dict.rendering.vertices;

      this.AllData.frameIdx=res.data.dict.rendering.frameIdx;

      this.HandleData();
    },

    //绘制表
    DoEchart(id){
      var ElementID = document.getElementById(id)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      //数据处理
      var seriesData = [],legendData=[]
      var titleName =''
      {
        if(id == 'setPassCall'){
          legendData=['SetPassCalls','drawCalls']
          titleName = "次数"
          seriesData[0] = {
            name: 'SetPassCalls',
            type: 'line',
            data: this.AllData.setPassCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[1] = {
            name: 'drawCalls',
            type: 'line',
            data: this.AllData.drawCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
         }
        if(id == 'Vertices'){
          var data1=[],data2=[]
          legendData=['Vertices','triangles']
          titleName = "数量"
          for(let i=0;i<this.AllData.triangles.length;i++){
            data1.push(this.AllData.vertices[i]*1000)
            data2.push(this.AllData.triangles[i]*1000)
          }
          seriesData[0] = {
            name: 'Vertices',
            type: 'line',
            data: data1,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[1] = {
            name: 'triangles',
            type: 'line',
            data: data2,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
         }
        if(id == 'VBOCount'){
          legendData=['VBOCount']
          titleName = "数量"
          seriesData[0] = {
            name: 'VBOCount',
            type: 'line',
            data: this.AllData.vboTotal,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
         }
        if(id == 'VBOSize'){
          var data3 =[]
          for(let i=0;i<this.AllData.vboTotalBytes.length;i++){
            data3.push((this.AllData.vboTotalBytes[i]/1024).toFixed(2))
          }
          legendData=['VBOSize']
          titleName = "内存占用总数(MB)"
          seriesData[0] = {
            name: 'VBOSize',
            type: 'line',
            data: data3,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            }
           }
         }
        
      }
      
      let option = {
        title: {
            text: titleName,
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"      
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: legendData,
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.AllData.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    }
  }
}
</script>


<style  lang="less"  scoped>
  .green{
    color:green
  }
  .red{
    color:red
  }
  .el-icon-s-unfold{
    color:#1341d8
  }
  .dataTab{
    margin-right:4%;margin-bottom:20px;width:18%;height:180px;float:left;justify-content:center;
    border-right:1px solid #eee;
    color: rgba(81, 105, 105, 0.979);
    
  }
</style>