<template>
    <div style="height:auto;min-height:1000px">
      <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>内存占用</el-breadcrumb-item>
      </el-breadcrumb> 
      <div style="display:flex;text-align:center;margin-top:50px">
        <div v-for="(item,index) in Memory_data" :key="index" style="border-right:1px solid #eee;height:160px;width:15%;float:left;margin:25px 0 0 5%;padding-right:4%">
          <div style="color:grey;font-size:15px">
            {{item.title}}
          </div>
          <div style="font-size:40px;margin-top:20px;color:#494a4e">{{item.value}}</div>
        </div>
      </div>
      <el-collapse v-model="activeNames1" style="margin-top:40px">
        <el-collapse-item name="1">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>Toal Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_toal"></div>
        </el-collapse-item>
        <el-collapse-item name="2">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>Unity Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_Unity"></div>
        </el-collapse-item>
        <el-collapse-item name="3">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>Mono Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_Mono"></div>
        </el-collapse-item>
        <el-collapse-item name="4">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>GFX Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_GFX"></div>
        </el-collapse-item>
        <el-collapse-item name="5">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>FMod Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_FMod"></div>
        </el-collapse-item>   
        <el-collapse-item name="6">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>Profiller Reserved/Used
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Memory_Profiller"></div>
        </el-collapse-item>               
      </el-collapse>
    </div>     
</template>
<script>
export default {
  data(){
return {  
      Memory_data:[{
        title:"ReservedTotal峰值(MB)",
        value:0,
        },{
          title:'ReservedUnity峰值(MB)',
          value:0,
        },{
          title:'ReservedMono峰值(MB)',
          value:0,
        },{
          title:'ReservedGFX峰值(MB)',
          value:0,
      }],
      Alldata:{
        bytesReservedTotal:[],
        bytesReservedUnity:[],
        bytesReservedFMOD:[],
        bytesReservedGFX:[],
        bytesReservedMono:[],
        bytesReservedProfiler:[],
        bytesReservedVideo:[],

        bytesUsedFMOD:[],
        bytesUsedGFX:[],
        bytesUsedMono:[],
        bytesUsedProfiler:[],
        bytesUsedTotal:[],
        bytesUsedUnity:[],
        bytesUsedVideo:[],

        frameIdx:[],
        animationClipsCount:[],
        animationClipsBytes:[],
        audioBytes:[],
        audioCount:[],
        materialBytes:[],
        materialCount:[],
        meshBytes:[],
        meshCount:[],
        textureBytes:[],
        textureCount:[],
      },
      activeNames1:["1","2","3","4","5","6"],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素  
      }
  },
  created(){
    this.getMemory();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    async getMemory(){
      const value={
        id:window.localStorage.getItem("id"),
        type:1,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.Alldata.bytesReservedTotal=res.data.dict.memory.bytesReservedTotal;
      this.Alldata.bytesReservedUnity=res.data.dict.memory.bytesReservedUnity;
      this.Alldata.bytesReservedFMOD=res.data.dict.memory.bytesReservedFMOD;
      this.Alldata.bytesReservedGFX=res.data.dict.memory.bytesReservedGFX;
      this.Alldata.bytesReservedMono=res.data.dict.memory.bytesReservedMono;
      this.Alldata.bytesReservedProfiler=res.data.dict.memory.bytesReservedProfiler;

      this.Alldata.bytesUsedFMOD=res.data.dict.memory.bytesUsedFMOD;
      this.Alldata.bytesUsedGFX=res.data.dict.memory.bytesUsedGFX;
      this.Alldata.bytesUsedMono=res.data.dict.memory.bytesUsedMono;
      this.Alldata.bytesUsedProfiler=res.data.dict.memory.bytesUsedProfiler;
      this.Alldata.bytesUsedTotal=res.data.dict.memory.bytesUsedTotal;
      this.Alldata.bytesUsedUnity=res.data.dict.memory.bytesUsedUnity;     

      this.Alldata.frameIdx=res.data.dict.memory.frameIdx;
      this.handleData();
    },
    handleData(){
      this.Memory_data[0].value = (Math.max.apply(null,this.Alldata.bytesReservedTotal)/1024/1024).toFixed(2)
      this.Memory_data[1].value = (Math.max.apply(null,this.Alldata.bytesReservedUnity)/1024/1024).toFixed(2)
      this.Memory_data[2].value = (Math.max.apply(null,this.Alldata.bytesReservedMono)/1024/1024).toFixed(2)
      this.Memory_data[3].value = (Math.max.apply(null,this.Alldata.bytesReservedGFX)/1024/1024).toFixed(2)

      setTimeout(()=>{
        this.DoEchart("Memory_toal");
        this.DoEchart("Memory_Unity");
        this.DoEchart("Memory_Mono");
        this.DoEchart("Memory_GFX");
        this.DoEchart("Memory_FMod");
        this.DoEchart("Memory_Profiller");
      },500)
    },
    // 折线图
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)

      var seriesData = []
      var legendData = []
      var data1=[],data2=[]
      {
        if(key=="Memory_toal"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedTotal[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedTotal[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedTotal","UsedTotal"]
          seriesData[0] = {
            name: 'ReservedTotal',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedTotal',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key=="Memory_Unity"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedUnity[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedUnity[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedUnity","UsedUnity"]
          seriesData[0] = {
            name: 'ReservedUnity',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedUnity',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key=="Memory_Mono"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedMono[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedMono[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedMono","UsedMono"]
          seriesData[0] = {
            name: 'ReservedMono',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedMono',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key=="Memory_GFX"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedGFX[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedGFX[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedGFX","UsedGFX"]
          seriesData[0] = {
            name: 'ReservedGFX',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedGFX',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key=="Memory_FMod"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedFMOD[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedFMOD[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedFMOD","UsedFMOD"]
          seriesData[0] = {
            name: 'ReservedFMOD',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedFMOD',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key=="Memory_Profiller"){
          for(let i=0;i<this.Alldata.bytesReservedTotal.length;i++){
            data1.push((this.Alldata.bytesReservedProfiler[i]/1021/1024).toFixed(2))
            data2.push((this.Alldata.bytesUsedProfiler[i]/1021/1024).toFixed(2))
          }
          legendData = ["ReservedProfiler","UsedProfiler"]
          seriesData[0] = {
            name: 'ReservedProfiler',
            type: 'line',
            data: data1,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          },
          seriesData[1] = {
            name: 'UsedProfiler',
            type: 'line',
            data: data2,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            },
          }
        }
      }
      

      let option = {
        title: {
            text: '内存占用总量(MB)',
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
             confine:true,
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"      
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: legendData
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.Alldata.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData,
      };
       this.BrokenLine.setOption(option)
    },
  }
}
</script>


