<template>
         <el-main style="overflow-x:hidden">
          <div  class="TypeTitle" style="margin:20px auto;font-size:40px;font-weight:700;width:30%;text-align:center">客户端安全性报告</div> 
          <el-card>
            <!-- 筛选栏 -->
            <div>
              <span >选择项目</span>
              <el-select v-model="setSelectInfo"   placeholder="选择项目" @change="selectProject">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.ProjectName" :value="item.ProjectName"> </el-option>
              </el-select>
              <!-- <el-select v-model="item0.setSelectInfo.VersionId"  clearable placeholder="选择版本" @change="selectProject1(index0)">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.game_name" :value="item.game_name"> </el-option>
              </el-select> -->
              <el-button type="primary" style="margin-left:100px" @click="reset">重置</el-button>  
            </div>
            <!-- 测试详情 -->
            <template>
              <el-table ref="reportTable" :data="ProjectInfo" style="width: 100%;cursor:pointer" highlight-current-row   @row-click="getReport" >
                <!-- <el-table-column type="index" label="序号"></el-table-column> -->
                <el-table-column  label="项目" width="auto"> 
                  <div slot-scope="scope" class="project">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <img  style="width:52px;height:52px" :src="'http://10.0.10.246/icon/'+scope.row.ImgPath" />
                      <span style="margin-left:10px ;font-weight:bold;color:blue" @click="Report(scope.row)">{{scope.row.ProjectName}}</span>
                    </div>
                  </div>
                </el-table-column>
                <el-table-column prop="Version" label="版本"  width="auto"></el-table-column>
                <el-table-column  prop="TestDate" label="测试日期" width="auto"></el-table-column>
                <el-table-column  prop="Result" label="测试结果" width="auto">
                    <div slot-scope="scope">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <span v-if="scope.row.Result==true" style="margin-left:10px ;font-weight:bold;color:green">成功</span>
                      <span v-if="scope.row.Result==false" style="margin-left:10px ;font-weight:bold;color:red">失败</span>
                    </div>
                  </div>
                </el-table-column>                 
              </el-table>
            </template>
            <!-- 页码区-->
            <el-pagination @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="ProjectInfo.length"
                :page-size="10"
                ></el-pagination>
          </el-card>  
        </el-main>
</template>
<script>
export default {
  data(){
return {  
        //自研项目列表 
        options1: [],
        // 选中数据
        setSelectInfo:'',
        // 修改项目信息
        setGameData:{},
        dialogVisable:false,
        pageInfo: {
          // 当前的页数
          pagenum: 1,
          // 当前每页显示多少条数据
          pagesize: 8, 
          total:3,
          currentData:[],
        },
                
        ProjectInfo:[],
      }

  },
  created(){
    this.getProjectList();
    this.getRecentProject();
    // this.getfilter();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
         //重置筛选数据
    reset(){
      this.setSelectInfo='',
      this.getRecentProject();
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.ProjectInfo.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    // 请求初始化信息
    async getProjectList(){
      const param={
        ProjectName:this.setSelectInfo,
        type:2
      }
      const {data:res} = await this.$http.get("init_project_other",{params:param})
      .catch((error) => {
          console.log('错误信息:'+error)
          }
      );
      // console.log(res)
      this.options1= res.data.dict.arrayProject;
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
    },
    // 请求最近数据
    async getRecentProject(){
      const param={
        ProjectName:this.setSelectInfo,
        type:2
      }
      const {data:res} = await this.$http.get("Recent_other_report",{params:param})
      .catch((error) => {
          console.log('错误信息:'+error)
          }
      );
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
      // console.log(res)
      this.ProjectInfo= res.data.dict.version_list;
      this.setCurrentPageData();

    },
    async selectProject(){
      const param={
        ProjectName:this.setSelectInfo,
        type:2
      }
      const {data:res} = await this.$http.get("select_other_report",{params:param})
      this.ProjectInfo= res.data.dict.version_list;
      this.setCurrentPageData();
    },
    getReport(val){     
      this.$router.push({path:'/Sadetail',query:{
            id:val.id,
      }})


      // let id = val.id
      // var {href} = this.$router.resolve({
      //   path:'/Sadetail',
      //   query:{
      //     id:id
      //   }
      // });
      // window.open(href)
    },
  }
}
</script>

