<template>
  <div>
    <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Ad_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/Ad_Devices' }">设备列表</el-breadcrumb-item>
        <el-breadcrumb-item>设备详情</el-breadcrumb-item>
      </el-breadcrumb> 
      <div style="height:auto;min-height:1000px;width:90%;margin:50px auto ">
        <div style="display:flex">
          <el-card style="height:240px;margin-right:1%;width:45%;min-width:600px;font-size:10px;font-weight:700;color:black;">
            <div style="display:inline-block;padding:5px;float:left">
                <div class="iconfont icon-jieguofabu " style="font-size:25px">测试结果</div>
                <div v-if="testData.Result" style="margin:40px 0 0 40px;font-size:40px;color:#7666d1">通过</div>
                <div v-else style="margin:40px 0 0 40px;font-size:40px;color:#c91c1c">失败</div>
            </div>
            <div style="padding:5px;margin:40px 0 0 70px;display:inline-block">
              <div class="iconfont icon-jixing" style="margin-bottom:10px;">设备型号：<span style="color:cornflowerblue;margin-left:25px">{{testData.DeviceName}}</span></div>
              <div class="iconfont icon-banben " style="margin-bottom:10px">系统版本：<span style="color:cornflowerblue;margin-left:25px">{{testData.SystemVersion}}</span></div>
              <div class="iconfont icon-RectangleCopy" style="margin-bottom:10px">分辨率：<span style="color:cornflowerblue;margin-left:25px">{{testData.Screen}}</span></div>
              <!-- <div class="iconfont icon-lishi1 " style="margin-bottom:10px">用例执行率：</div> -->
              <div class="iconfont icon-riqi1 " style="margin-bottom:0px">测试日期：<span style="color:cornflowerblue;margin-left:25px">{{testData.TestDate}}</span></div>
            </div>
          </el-card>
          <el-card style="height:240px;width:50%;min-width:600px;font-weight:700;color:black;">
            <div style="font-size:25px"  class="iconfont icon-xingneng" >性能数据</div>
            <div style="margin-left:140px">
              <div style="height:80px;width:200px;float:left">CPU占用率[%]
                <div style="color:green" v-if="testData.Performance.CurrentAvg.cpu<testData.Performance.HistoryAvg.cpu">{{testData.Performance.CurrentAvg.cpu}}</div>
                <div style="color:red" v-else>{{testData.Performance.CurrentAvg.cpu}}</div>
                <div style="color:grey">历史平均：{{testData.Performance.HistoryAvg.cpu}}</div>
              </div>
              <div style="height:80px;width:200px;float:left">流量[KB]
                <div style="color:green" v-if="testData.Performance.CurrentAvg.data<testData.Performance.HistoryAvg.data">{{testData.Performance.CurrentAvg.data}}</div>
                <div style="color:red" v-else>{{testData.Performance.CurrentAvg.data}}</div>
                <div style="color:grey">历史平均：{{testData.Performance.HistoryAvg.data}}</div>
              </div>
              <div style="height:80px;width:200px;float:left">内存占用[MB]
                <div style="color:green" v-if="testData.Performance.CurrentAvg.memory<testData.Performance.HistoryAvg.memory">{{testData.Performance.CurrentAvg.memory}}</div>
                <div style="color:red" v-else>{{testData.Performance.CurrentAvg.memory}}</div>
                <div style="color:grey">历史平均：{{testData.Performance.HistoryAvg.memory}}</div>
              </div>
              <div style="height:80px;width:200px;float:left">帧速率[FPS]
                <div style="color:green" v-if="testData.Performance.CurrentAvg.fps<testData.Performance.HistoryAvg.fps">{{testData.Performance.CurrentAvg.fps}}</div>
                <div style="color:red" v-else>{{testData.Performance.CurrentAvg.fps}}</div>
                <div style="color:grey">历史平均：{{testData.Performance.HistoryAvg.fps}}</div>
              </div>
            </div>
          </el-card>
        </div>


        <el-tabs v-model="activeNames"  @tab-click="clickTabs" stretch style="justify-content:center;margin-top:50px;z-index:-999">
          <el-tab-pane  label="问题定位"  name="first" style="padding:40px">
            <div>测试步骤
              <el-table :data="pageInfo.currentData"  style="width: 100%" stripe>
                <el-table-column type="index" label="序号" align="center" width="300"></el-table-column>
                <el-table-column prop="stepName" label="测试步骤" align="center" width="400"></el-table-column>
                <el-table-column  label="测试结果" align="center">
                  <template slot-scope="scope">
                    <span v-if="scope.row.result==true" style="color:#67c32A;" >成功</span>
                    <el-button v-if="scope.row.result==false" style="color:red" type="text" size="middle" >失败</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <!-- 页码区-->
              <el-pagination  :hide-on-single-page="pageInfo.currentData.length<=pageInfo.pagesize" @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="this.testData.Step.length"></el-pagination>
            </div>
            <div v-if="testData.ErrorInfo" style="margin-top:40px">问题详情
              <div style="margin-top:30px;height:300px;display:flex;padding:10px">
                <div style="padding:30px;width:45%;border-right:2px solid #eee"><img v-image-preview style="width:360px;cursor:pointer;"   :src="'http://10.0.10.246'+testData.DevicePath+'/'+testData.ErrorInfo.ScreenShot" /></div>
                <div style="padding:30px;margin-left:80px;color:grey;min-width:200px">
                  <div style="margin-bottom:20px">问题类型：<span style="color:red" >{{testData.ErrorInfo.FailType}}</span> </div>
                  <div style="margin-bottom:20px">问题步骤：<span style="color:red">【{{testData.ErrorInfo.Step}}】</span> </div>
                  <div style="margin-bottom:20px">问题时间：<span style="color:red">{{testData.ErrorInfo.Time}}</span> </div>
                  <div>查看日志：<a :href="'http://10.0.10.246'+testData.DevicePath+'/DeviceLog.log'" target="_blank" style="text-decoration:none;color:#0070f0">查看详情</a> </div>
                </div>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane  label="性能详情"  name="second" style="padding:40px">
            <div>
              <div>FPS</div>
              <div style="height:300px " id="FPS"></div>
            </div>
            <div>
              <div>CPU占用</div>
              <div  style="height:300px " id="CPU"></div>
            </div>
            <div>
              <div >上行流量</div>
              <div  style="height:300px " id="NetUp"></div>
            </div>
            <div>
              <div >下行流量</div>
              <div  style="height:300px " id="NetDown"></div>
            </div>
            <div>
              <div >内存占用</div>
              <div  style="height:300px " id="Memory"></div>
            </div>
          </el-tab-pane>
          <el-tab-pane  label="测试截图"  name="third" style="padding:40px;dispaly:flex">
            <div  v-for="(item,index) in arryImg " :key="index" style="float:left">
              <img v-image-preview style="width:240px;cursor:pointer;margin-left:25px" :src="item" />
              <div style="margin-left:12%">{{testData.ArrayScreenShot[index]}}</div>
            </div>
          </el-tab-pane>
      </el-tabs>
      </div>
  </div>     
</template>
<script>
export default {
  data(){
    return {  
      Device_id:null,
      arryImg:[],
      pageInfo: {
        pagenum: 1,
        pagesize: 10, 
        currentData:[],
      },

      errorData:{
        FailType:'卡死',
        step:'查看结果',
        errortime:'2020-10-22 15:12:33',
      },
      activeNames:'first',
      testData:{
        FailType:'',
        Result:false,
        DeviceName:'华为mate 30',
        SystemVersion:'Android 8.0',
        Screen:"2048x1024",
        TestDate:'2020-10-22 16:10:25',
        DevicePath:null,
        Performance:{
          CurrentAvg:{
            cpu:12.8,
            data:800,
            memory:500,
            fps:25,
          },
          HistoryAvg:{
            cpu:12,
            data:900,
            memory:600,
            fps:30
          },
          timestamp:[1,2,13,4,5,6,7,8,9],
          cpu:[1,5,13,6,5,6,7,8,9],
          recv:[1,12,3,4,88,6,7,8,9],
          send:[1,12,3,4,88,6,7,8,9],
          memory:[1,2,13,4,7,6,7,8,9],
          fps:[1,2,3,4,5,60,71,18,9]

        },
        ArrayScreenShot:[],
        Step:[{setpName:'选择装备',result:true},{setpName:'开始战斗',result:true},{setpName:'查看结果',result:false},]
      },
    }
  },
  created(){
    this.Device_id=this.$route.query.Deviceid;
    this.getdevicesInfo();
  },
  computed:{

  },
  methods:{
     // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.testData.Step.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },
    async getdevicesInfo(){
      let id = {
        id:this.Device_id
      }
      const {data:res} = await this.$http.get('deviceDetail_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.testData = res.data.dict.Test_Info
      this.arryImg.length=this.testData.ArrayScreenShot.length;
      for(let i=0;i<this.testData.ArrayScreenShot.length;i++){
        let imgPath = "http://10.0.10.246"+this.testData.DevicePath+'/'+this.testData.ArrayScreenShot[i]+'.png'
        this.arryImg[i]= imgPath
      }


      this.setCurrentPageData();
    },





    clickTabs(val){ //0,1,2
      if(val.index==1){
        setTimeout(()=>{
          this.DoEchart("FPS")
          this.DoEchart("CPU")
          this.DoEchart("NetUp")
          this.DoEchart("NetDown")
          this.DoEchart("Memory")
        },200)
      }
    },
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let xData=[]
      for(let i=0;i<this.testData.Performance.timestamp.length;i++){
        let a =this.testData.Performance.timestamp[i]-this.testData.Performance.timestamp[0]
        let min = parseInt(a/(1000*60))
        if(min<10){
          min = '0'+min
        }
        let second=parseInt((a%(1000*60))/1000)
        if(second<10){
          second = '0'+second
        }
        let x = min+":"+second
        xData.push(x)
      } 

      let value=null;
      let Dataname=null;
      let color=null;
      let Tab=null;
      if(key=='FPS'){
        value = this.testData.Performance.fps
        Dataname = '帧数率'
        color="#6b58e8"
        Tab='fps'
      }
      if(key=='CPU'){
        value = this.testData.Performance.cpu
        Dataname = 'CPU'
        color="#82a6bc"
        Tab='%'
      }
      if(key=='NetUp'){
        value = this.testData.Performance.send
        Dataname = '上行流量'
        color="#46cace"
        Tab='KB'
      }
      if(key=='NetDown'){
        value = this.testData.Performance.recv
        Dataname = '下行流量'
        color="#46ce68"
        Tab='KB'
      }
      if(key=='Memory'){
        value = this.testData.Performance.memory
        Dataname = '内存'
        color="#e5e3ae"
        Tab='MB'
      }

      let option = {
        tooltip: {
            trigger: 'axis',
        },
        legend: {
            data: ['帧数率','CPU','上行流量','下行流量','内存' ]
        },
        color:color,
        toolbox: {
            show: true,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                boundaryGap:false,
                data:xData
            }
        ],
        yAxis: [
            {
                type: 'value',
                axisLabel:{formatter:'{value}'+' '+Tab}
            }
        ],
        series: {
          name:Dataname ,
          type: 'line',
          data: value,
          markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
          },
          markLine:{
            data:[{type:"average",name:'平均值'}]
          }
        }
      };
       this.BrokenLine.setOption(option)
    }

  }
}
</script>


