<template>
        <el-main style="width:100%;overflow-x:hidden;height:100%">
          <div style="height:30px;background:snow;margin:-5px 0 0 -5px;">
             <el-button type="text" size="mini" style="margin-top:0px;float:right;font-size:18px;color:#4276e5" @click="to_OldReport">点击跳转至旧版适配报告页面 >>></el-button>
            <!-- 页码区-->
          </div> 

          <div class="TypeTitle" style="margin:-5px auto 20px auto;font-size:40px;font-weight:700;width:20%;;text-align:center;">兼容性适配报告</div> 

          <el-card >
              <span >选择项目</span>
              <el-select v-model="setSelectInfo.ProjectName"   placeholder="选择项目" @change="selectProject()">
                <el-option
                  v-for="(item,index) in options" :key="index" :label="item.ProjectName" :value="item.ProjectName"> </el-option>
              </el-select>-
              <el-select v-model="setSelectInfo.Version"   placeholder="选择版本" @change="selectProject1()">
                <el-option
                  v-for="(item,index) in optionsVersion" :key="index" :label="item" :value="item" > </el-option>
              </el-select>
              <el-button type="primary" style="margin-left:100px" @click="reset">重置</el-button>  

              <template>
              <el-table ref="reportTable" :data="pageInfo.currentData" style="width: 100%;cursor:pointer" highlight-current-row  @row-click="getReport" >
                <el-table-column type="index" label="序号"></el-table-column>
                <!-- <el-table-column type="selection"></el-table-column> -->
                <el-table-column  label="项目名称" width="auto"> 
                  <div slot-scope="scope" class="project">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <img  style="width:52px;height:52px" :src="(imgUrl+scope.row.ImgPath)" />
                      <span style="margin-left:10px ;font-weight:bold;color:blue">{{scope.row.ProjectName}}</span>
                    </div>
                  </div>
                </el-table-column>
                <el-table-column prop="TestVersion" label="版本"  width="auto"></el-table-column>
                <el-table-column  label="平台" width="auto">
                    <div slot-scope="scope">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <span v-if="scope.row.platform==1" >ios</span>
                      <span v-if="scope.row.platform==2">Android</span>
                    </div>
                  </div>
                </el-table-column>
                <el-table-column  prop="TestDate" label="测试日期" width="auto"></el-table-column>
                <el-table-column  prop="pass_rate" label="设备通过率(%)" width="auto"></el-table-column>
                <el-table-column  prop="Result" label="测试结果" width="auto">
                    <div slot-scope="scope">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <span v-if="scope.row.Result==true" style="margin-left:10px ;font-weight:bold;color:green">成功</span>
                      <span v-if="scope.row.Result==false" style="margin-left:10px ;font-weight:bold;color:red">失败</span>
                    </div>
                  </div>
                </el-table-column>
              </el-table>
            </template>
            <!-- 页码区-->
            <el-pagination @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="tableData.length"
                :page-size="10"
                >
            </el-pagination>
          </el-card>
        </el-main>
</template>
<script>
export default {
  data(){
return {  
        options:[],
        optionsVersion:[],
        // 选中数据
        setSelectInfo:{
          ProjectName:'',
          Version:'',
        },
        tableData: [],
        //测试
        pageInfo: {
          // 当前的页数
          pagenum: 1,
          // 当前每页显示多少条数据
          pagesize: 8, 
          total:3,
          currentData:[],
        },
        imgUrl:'http://10.0.10.246:80/icon/',
      }

  },
  created(){
    this.get_initProject();
    this.get_recentProject();
  },
  computed:{
    upData() {
      return{
      }
    },
  },
  methods:{
    to_OldReport(){
        var url = "http://10.0.10.246"
        window.open(url ,'_blank')
    },
    reset(){
      this.setSelectInfo.ProjectName="";
      this.setSelectInfo.Version="";
      this.optionsVersion=[]
      this.get_recentProject();
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.tableData.slice(begin,end); 
      //console.log(this.pageInfo.currentData)
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    async get_initProject(){
      const{data:res} = await this.$http.get('init_project_adaptive')
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.options=res.data.dict.arrayProject
    },

    async get_recentProject(){
      const{data:res} = await this.$http.get('Recent_adaptive_report')
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.tableData=res.data.dict.version_list
      this.setCurrentPageData();
    },

    async select_Project(){
      this.tableData=[]
      const{data:res} = await this.$http.get('select_adaptive_report',{params:this.setSelectInfo})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.tableData=res.data.dict.arrayProject
      console.log(this.tableData)
      this.setCurrentPageData();
    },

    async selectProject(){
      for(let i=0;i<this.options.length;i++){
        if(this.setSelectInfo.ProjectName==this.options[i].ProjectName){
          this.optionsVersion=this.options[i].Version
        }
      }
      this.setSelectInfo.Version='';
      this.select_Project();

    },

    selectProject1(){
      this.select_Project();
    },
    getReport(val){
      // console.log(val)
      this.$router.push({path:'/Ad_detail',query:{
            id:val.id,
            ProjectName:val.ProjectName,
            Version:val.TestVersion,

      }})

      //console.log(val);
      // var {href} = this.$router.resolve({
      //     path:'/Ad_detail',
      //     query:{
      //       id:val.id,
      //       ProjectName:val.ProjectName,
      //       Version:val.Version,
      //     }
      //   });
      //   window.open(href)
    },
  }
}
</script>

<style>
  .el-header, .el-footer {
    background-color: rgb(23, 24, 22);
    color: rgb(255, 255, 254);
    text-align: center;
    line-height: 60px;
    width: 100%;
  }
  .el-main {
    background-color:#fffafa;
    width: 100%;
  }
  .el-card{
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100%;
  }
  .el-select{
    margin:5px 5px 10px 10px;
    width: 210px;
  }
  .select{
    width: 140px;
  }
  .project{
    display: flex;
    align-items: center;
  }
  .imgHeader {
			width: 170px;
			margin-left: -18px;
			margin-top: 0px;
      float: left;
  }
</style>