            <el-table-column prop="name" label="函数名称"></el-table-column>
            <el-table-column prop="callNum" align="center">
              <template slot="header">
                <span>调用次数</span>
                <el-tooltip effect="dark"  placement="right">
                 <i class="el-icon-info"></i>
                 <div  slot="content">
                   <div>函数在测试区间内被调用次数</div>
                 </div>
               </el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="selfsum" align="center">
              <template slot="header">
                <span>self总耗时(ms)</span>
                <el-tooltip effect="dark"  placement="right">
                 <i class="el-icon-info"></i>
