                   <div>函数运行总时长 / 总运行帧数</div>
                 </div>
               </el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="totalsum" align="center">
              <template slot="header">
                <span>Total总耗时(ms)</span>
                <el-tooltip effect="dark"  placement="right">
