<script>
export default {
  data(){
return {  
      isCollapse: false,
      SetFunction:[
        {
          module:"Physics",
          name:"FixedUpdate.PhysicsFixedUpdate",
          explain:'物理模块的FixedUpdate。'
        },{
          module:"Animation",
          name:"MeshSkinning.Update",
          explain:'蒙皮网格的每帧更新。'
          
        },{
          module:"Particle",
          name:"ParticleSystem.Update",
          explain:'更新粒子系统。'
        },{
          module:"Loading",
          name:"Loading.UpdatePreloading",
          explain:'Unity引擎最主要的加载函数。一般在切换场景时或主动动态加载资源时较大;一般来说，加载资源越多、越复杂，则其反映的Loading.UpdatePreloading耗时则越大'
        }
        ,{
          module:"Memory",
          name:"GC.Collect",
          explain:'直接控制垃圾回收器。'
        }
      ],
      FuncList:[],
      Func:[],
      //   Func:[{
      //   name:null,
      //   callNum:null,
      //   selfsum:null,
      //   selfperFrame:null,
      //   totalsum:null,
      //   totalperframe:null,
      // }],
      SelectFunc_name:null,
      reflush : true
    }
  },
  created(){
    this.getFunc();
    // setTimeout(()=>{
    //   this.$forceUpdate();
    //   console.log(111)
    // },500)
  },
  mounted() {
   // this.FuncList 
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    async getFunc(){
      const value={
        id:window.localStorage.getItem("id"),
        type:2,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value}).catch((error)=>{
          console.log(error)
        });
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.handeleFunc(res)
    },
    handeleFunc(res){
      this.FuncList.length = res.data.dict.main_self.name.length
      for(let i=0;i<res.data.dict.main_self.name.length;i++){
        this.FuncList[i] = Object.assign({},this.Func[0]) //只传值
        this.FuncList[i].name = res.data.dict.main_self.name[i]
        this.FuncList[i].callNum = res.data.dict.main_self.callNum[i]
        this.FuncList[i].selfsum = res.data.dict.main_self.selfsum[i]
        this.FuncList[i].selfperFrame = res.data.dict.main_self.selfperFrame[i]
        this.FuncList[i].totalsum = res.data.dict.main_self.totalsum[i]
        this.FuncList[i].totalperframe = res.data.dict.main_self.totalperFrame[i]
      }
      this.$forceUpdate();
    },
    
    Search(value){
     // console.log(value)
      this.Func=[]
      let a=0
      let b=0
      for(let i=0;i<this.FuncList.length;i++){
        if(value==this.FuncList[i].name){
         this.Func[a] = this.FuncList[i]
         a+=1
         b=1
        }
      }
      this.reflush = !this.reflush
      if(b==0){
        alert("测试区间未找到该函数或该函数耗时低于TOP500")
      }
    }
  }
}
</script>


