                          <span v-if="index==3 && item.CurrentAvg>=item.TypeAvg" style="color:green;font-weight:700;font-size:30px">{{item.CurrentAvg}}</span>
                          <span v-if="index==3 && item.CurrentAvg<item.TypeAvg" style="color:red;font-weight:700;font-size:30px">{{item.CurrentAvg}}</span>
                        </div>
                        <div style="font-size:15px;color:grey;margin:5px 0 0 10%">{{item.title2}}:
                          <span>{{item.TypeAvg}}</span>
                        </div>
                        <div style="font-size:15px;color:grey;margin:5px 0 0 10%">{{item.title3}}:
                          <span>{{item.HistoryAvg}}</span>
                        </div>
                      </el-card>
                  </div>
                  <div style="font-style:italic;font-size:10px;float:right">注：同类平均基于该游戏竞品数据(性能数据平台可查看)，历史平均基于该游戏历史版本数据</div>
                  </router-link>    
                </div>
          </div> 

          <div style="margin-top:60px;width:100%;border-bottom:0px solid #eee;height:300px">
            <div class="iconfont icon-keliuqushi" style="font-weight:700;color:grey;">适配率趋势</div>
            <div style="height:280px;width:100%;display:inline-block;" id="TrendChart"></div>
          </div> 

          <div style="margin-top:40px;width:100%;">
            <div class="iconfont icon-step " style="font-weight:700;color:grey;">测试步骤</div>
            <el-table :data="pageInfo.currentData"  style="width: 100%" stripe>
                <el-table-column type="index" label="序号" align="center" width="300"></el-table-column>
                <el-table-column prop="stepName" label="测试步骤" align="center" width="400"></el-table-column>
                <el-table-column  label="测试结果" align="center">
                  <template slot-scope="scope">
                    <span v-if="scope.row.result==true" style="color:#67c32A;" >成功</span>
                    <router-link  to="/Ad_Problem" v-if="scope.row.result==false" style="color:red;text-decoration:none" type="text" size="middle" >失败</router-link>
                  </template>
                </el-table-column>
                <el-table-column prop="ProblemDevices" label="问题设备数" align="center" width="400">
                  <template slot-scope="scope">
                    <span v-if="scope.row.ProblemDevices==null" >-</span>
                    <router-link  v-else to="/Ad_Problem"  style="color:red;text-decoration:none" >{{scope.row.ProblemDevices}}</router-link>
                  </template>
                </el-table-column>
            </el-table>
            <!-- 页码区-->
            <el-pagination  @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="TestStep.length"></el-pagination>
          </div>        
        </div>
    </div>
 
</template>
<script>
export default {
  data(){
return {  
      pageInfo: {
        pagenum: 1,
        pagesize: 5, 
        currentData:[],
      },
      data:{
        Result:false,
        Version:'1.0.0',
        Date:'2020-10-20',
        Platform:'Android',
        DevicesNum:100,
        ProblemDevices:5,
        PassRate:null,
        DeviceRequire:'骁龙840及以上，安卓7.0及以上',
        PassRateHistory:null,
        error_type:null,
      },
      Brand:null,
      MainCard:[{
        title1:'CPU占用率[%]',
        title2:'同类平均',
        title3:'历史平均',
        CurrentAvg:13,
        TypeAvg:13,
        HistoryAvg:136,
        icon:"iconfont icon-cpu"
      },
      {
        title1:'数据流量[KB]',
        title2:'同类平均',
        title3:'历史平均',
        CurrentAvg:30,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-dbfx"
      },
      {
        title1:'内存占用[MB]',
        title2:'同类平均',
        title3:'历史平均',
        CurrentAvg:16,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-RectangleCopy"
      },
      {
        title1:'帧速率[FPS]',
        title2:'同类平均',
        title3:'历史平均',
        CurrentAvg:100,
        TypeAvg:136,
        HistoryAvg:136,
        icon:"iconfont icon-xingneng"
      },
      // {
      //   title1:'GPU占用率[%]',
      //   title2:'同类平均',
      //   title3:'历史平均',
      //   CurrentAvg:'',
      //   TypeAvg:'',
      //   HistoryAvg:'',
      // },
      ],
      trend:{
        version:["v1.0.0","v1.1.0","v1.2.0","v1.3.0","v1.4.0","v1.5.0"],
        value:[50,100,100,99,98,100]
      },
      TestStep:[{setpName:'进入剧情',result:true},{setpName:'选择角色',result:true},{setpName:'选择装备',result:true},{setpName:'开始战斗',result:true},{setpName:'查看结果',result:false},]

    }

  },
  created(){
    this.getTestInfo();

  },
  computed:{
  },
  methods:{
     // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.TestStep.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },
    DoEchart_PassRatePie(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var rate=this.data.PassRate
      let option = {
        legend:{
         // orient:'vertical',
          left:'right',
          bottom:10,
          data:['通过','不通过']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        color:["#8da2e9",'#c5cce2'],
        series: [
          {
            type: 'pie',
            radius: ['40%', '75%'],
            center:['50%','60%'],
            data: [{
                value: rate, 
                name: '通过',
                label:{
                   // color:"red",
                    show:true,
                    formatter:'通过率：{d}%'
                }},
                {value: 100-rate,
                name:'不通过',
                label:{
                  show:false,
                }
                },
            ],
            label:{
                show:true,
                position:"center",
                fontSize:"15",
                fontWeight:"bold"
            }
          }
        ]}
      this.BrokenLine.setOption(option)
    },
    DoEchart_ProblemPie(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let problemdata= [
          {value: this.data.error_type.InstallFail, name: '安装失败'},
          {value: this.data.error_type.LaunchFail, name: '启动失败'},
          {value: this.data.error_type.ForceExit, name: '闪退'},
          {value: this.data.error_type.Black, name: '黑屏'},
          {value: this.data.error_type.FuncError, name: '功能错误'},
          {value: this.data.error_type.NoRep, name: '卡死'},
          {value: this.data.error_type.UiError, name: 'UI异常'},
          {value: this.data.error_type.Other, name: '其他'},
      ]
      let Noproblemdata=[{value:0,name:"无异常问题"}]
      let devices=[
          {value: this.Brand.DevicesNum[0], name: this.Brand.brand[0]},
          {value: this.Brand.DevicesNum[1], name: this.Brand.brand[1]},
          {value: this.Brand.DevicesNum[2], name: this.Brand.brand[2]},
          {value: this.Brand.DevicesNum[3], name: this.Brand.brand[3]},
          {value: this.Brand.DevicesNum[4], name: this.Brand.brand[4]},
          {value: this.Brand.DevicesNum[5], name: this.Brand.brand[5]},
      ]

      let data=[]
      let Color= []
      let Name=''
      let LegendData=[]
      if(key=="ProblemPie"){
        Color = ["#c9b5b6 ","#cec8c8 ","#a09a9a ","#916a6a","#74c5d3 ","#5b919b  ","#7dc1a7 ",'#b4c187 ']
        Name = '问题类型'
        LegendData = ['安装失败', '启动失败', '闪退', '黑屏', '功能错误', '卡死', 'UI异常', '其他','无异常问题']
        if(this.data.Result==true){
          data = Noproblemdata
          Color = ["#2cea39"]
        }else{
          data= problemdata
        }
      }
      if(key=="devicesPie"){
          Color = ["#840344 ","#3730a5 ","#215682 ","#217e82","#267c3a ","#343d22  ","#bcb045 ",'#bf3fa9 ']
          LegendData = this.Brand.brand
          Name = '品牌'
          data = devices
      }

      let option = {
          tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b}: {c} ({d}%)'
          },
          color:Color,
          toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                restore: {show: true},
                saveAsImage: {show: true}
            }
          },
          legend: {
              orient: 'vertical',
              right: 10,
              data: LegendData
          },
          series: [
              {
                  name:Name,
                  type: 'pie',
                  radius:'65%',
                  center:['50%','50%'],
                  selectMode:'single',
                  emphasis: {
                      itemsStyle: {
                          shadowBlur:10,
                          shadowOffsetx:0,
                          shadowColor: 'rgba(0,0,0,0.5)'
                      }
                  },
                  data:data
              }
          ]
      };

      this.BrokenLine.setOption(option)
    },
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let option = {
        tooltip: {
            trigger: 'axis',
        },
        legend: {
            data: ['适配率走势' ]
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                boundaryGap:false,
                data:this.trend.arrayVersion
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: {
          name: "适配率走势",
          type: 'line',
          data:this.trend.arrayPassRate ,
          markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
          },
          markLine:{
            data:[{type:"average",name:'平均值'}]
          }
        }
      };
       this.BrokenLine.setOption(option)
    },

    async getTestInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('testinfo_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.data.Result=res.data.dict.res.Summery.Result
      this.data.Date=res.data.dict.res.Summery.Date
      this.data.DevicesRequire=res.data.dict.res.Summery.DeviceRequire
      this.data.PassRate=res.data.dict.res.Summery.PassRate
      this.data.ProblemDevices=res.data.dict.res.Summery.ProblemDevices
      this.data.DevicesNum=res.data.dict.res.Summery.DevicesNum
      this.data.Platform=res.data.dict.res.Summery.Platform
      this.data.Version=res.data.dict.res.Summery.Version
      this.data.PassRateHistory=res.data.dict.res.Summery.PassRateHistory
      this.data.error_type = res.data.dict.res.error_type

      this.TestStep = res.data.dict.res.TestStep
      this.trend = res.data.dict.res.Trend
      this.MainCard[0].CurrentAvg = res.data.dict.res.cpu.Current
      this.MainCard[0].HistoryAvg = res.data.dict.res.cpu.HistoryAvg
      this.MainCard[0].TypeAvg = res.data.dict.res.cpu.TypeAvg
      this.MainCard[1].CurrentAvg = res.data.dict.res.data.Current
      this.MainCard[1].HistoryAvg = res.data.dict.res.data.HistoryAvg
      this.MainCard[1].TypeAvg = res.data.dict.res.data.TypeAvg
      this.MainCard[3].CurrentAvg = res.data.dict.res.fps.Current
      this.MainCard[3].HistoryAvg = res.data.dict.res.fps.HistoryAvg
      this.MainCard[3].TypeAvg = res.data.dict.res.fps.TypeAvg
      this.MainCard[2].CurrentAvg = res.data.dict.res.memory.Current
      this.MainCard[2].HistoryAvg = res.data.dict.res.memory.HistoryAvg
      this.MainCard[2].TypeAvg = res.data.dict.res.memory.TypeAvg

      this.Brand =  res.data.dict.res.brand




    setTimeout(()=>{
      this.DoEchart_PassRatePie("PassRatePie");
      this.DoEchart_ProblemPie("ProblemPie");
      this.DoEchart_ProblemPie("devicesPie");
      this.DoEchart("TrendChart")
    },200)
      this.setCurrentPageData()
    }


  }
  
}
</script>


