               </el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="totalperframe" sortable align="center">
              <template slot="header">
                <span>Total每帧均耗时(ms)</span>
                <el-tooltip effect="dark"  placement="right">
                 <i class="el-icon-info"></i>
                 <div  slot="content">
                   <div>函数运行总时长 / 总运行帧数</div>
                 </div>
               </el-tooltip>
              </template>
            </el-table-column>
