</template>
<script>
export default {
  name: "Summary",
  data(){
    return {
      // 数据面板
      MainCard:[{
          title:"FPS(帧)",
          desc:"项目中的平均帧率。",
          data:0,
          sugdata:25,
          unit:'帧',
          sug:["1.查看CPU占用Top函数，从高到低开始依次分析和优化。","2.查看GC占用Top函数，GC是大部分卡顿的主要原因。"
          ,"3.查看脚本所造成的性能损耗。","4.降低游戏的渲染分辨率，手机帧率低的时候，减少每一帧要处理的像素数量。"
          ]
        },{
          title:"ReservedMono峰值(MB)",
          desc:"项目运行时Mono管理和分配的托管堆内存。",
          data:0,
          sugdata:80,
          unit:'MB',
          sug:["1.Unity项目中的内存占用主要由静态资源、AssetBundle 和Mono堆内存组成。",
          "2.查看Mono堆内存使用的合理性：ReservedMono峰值是否超过了UPR推荐值，ReservedMono是否存在持续上升的趋势。",
          "3.Mono堆内存占用量的主要来自于配置文件的读取和解析操作，查看配置文件读取操作的堆内存使用的合理性。",
          "4.查看AssetBundle在内存中的驻留情况。"
          ]
        },{
          title:"Texture峰值(MB)",//纹理资源
          desc:"项目中纹理资源的峰值。",
          data:0,
          sugdata:50,
          unit:'MB',
          sug:["决定纹理资源大小的因素主要有三种：分辨率、格式和Mipmap是否开启。",
          "1.降低分辨率，根据Game摄像机距离物体最近时，物体所占的像素大小来确定最大分辨率。",
          "2.拆分透明通道(Alpha)， 因为带Alpha通道的贴图 Unity会默认选择RGBA格式。",
          "3.调整压缩格式，尽量选用当前平台支持的最高压缩格式。",
          "4.禁用Mipmap",
          "5.启用Use Crunch Compression，Crunch是Unity支持的最新压缩格式，压缩比非常高"]
        },{
          title:"Mesh峰值(MB)",//网格资源
          desc:"项目中网格资源的峰值。",
          data:0,
          sugdata:20,
          unit:'MB',
          istrue:false,
          sug:["1. 减少顶点，在可以实现美术效果的前提下，顶点越少越好。",
          "2. 开启Optimize Mesh选项。"]
        },{
          title:"Animation峰值(MB)",
          desc:"项目中动画资源的峰值。",
          data:0,
          sugdata:15,
          unit:'MB',
          istrue:false,
          sug:["1. 减小动画长度",
          "2. 减少骨骼数量，删除无效的骨骼点。",
          "3. 减少关键帧密度，并减少导入的冗余关键帧。",
          "4. 减少动画精度，降低动画大小。"]
        },{
          title:"Audio峰值(MB)",
          desc:"项目中音频资源的峰值。",
          data:0,
          sugdata:15,
          unit:'MB',
          istrue:false,
          sug:["1.减少音频同时使用的数量，加大压缩比率。",
          "2.Load Type 改为Streaming，可以极大降低内存峰值。",
          "3.建议较长的音频使用.mp3或.ogg格式，较短的音频使用.wav 或.aiff格式。"]
        },{
          title:"DrawCall峰值(次)",
          desc:"项目中Draw Call峰值。",
          data:0,
          sugdata:250,
          unit:'次',
          istrue:false,
          sug:["1.UI DrawCall数量建议控制在15-40之间，UI DrawCall过多或过少，都会增大性能的消耗。",
          "2.查看渲染对象的DrawCall合批是否正确。",
          ]
        },{
          title:"Tris峰值(面)",
          desc:"",
          data:0,
          sugdata:200000,
          unit:'面',
          istrue:false,
          sug:["1.查看其渲染网格数量的合理性。",
          "2.加大对Game Object的裁剪力度。",
          "3.简化场景网格。"
          ]
        }
      ],
      // 获取页面传过来的id值
      id:null,
      isCollapse: false,
      testdata:{
        fps:0,
        max_reserved_mono:0,
        max_texture:0,
        max_mesh:0,
        max_animation:0,
        max_audio:0,
        max_draw_call:0,
        max_tris:0
      },
      SummarytData:{},
      CPUSummary:{
        frameIdx:[],
        frameTime:[],
        renderingTime:[],
        scriptTime:[],
        physicsTime:[],
      },
      testimglist:['http://10.0.10.246/icon/小猫爱消除.png','http://10.0.10.246/icon/疯狂原始人.png','http://10.0.10.246/icon/梦幻花园.png','http://10.0.10.246/icon/崩坏3.png'],
      testData1:[],
      color_Object:{
        green:'green',
        red:'red',
      },
      // 函数列表字段模板
      model : {
        name:'',
        callNum:'',
        selfsum:'',
        selfperFrame:'',
        totalsum:'',
        totalperframe:'',
      },
      FuncTop30test : [{
        name:'1',
        callNum:'1',
        selfsum:'1',
        selfperFrame:'1',
        totalsum:'1',
        totalperframe:'1',
      }],
      FuncTop30:[],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素
      testdata3:'',
        //测试数据模板
      csvdata:'',
      datamodel:{
        CPUtotal:[],
        Render:[],
        Audio:[],
        Other:[]
      }
    }
  },
  created(){
    this.id = window.localStorage.getItem("id");
    this.getSummary();
    this.getCPU();
    //this.setimglist();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    // 请求后端概要数据
    async getSummary(){
      const{data:res} = await this.$http.get("get_u3d_overview",{
        params:this.id})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.SummarytData = res.data.dict.overview

      //console.log(this.SummarytData)
      this.HandleData()
      this.select_color();
     
      this.FuncTop30.length = res.data.dict.model_dict.main_self.name.length
      for(let i=0;i<res.data.dict.model_dict.main_self.name.length;i++){
        this.FuncTop30[i] = Object.assign({},this.model) //只传值
        this.FuncTop30[i].name = res.data.dict.model_dict.main_self.name[i]
        this.FuncTop30[i].callNum = res.data.dict.model_dict.main_self.callNum[i]
        this.FuncTop30[i].selfsum = res.data.dict.model_dict.main_self.selfsum[i]
        this.FuncTop30[i].selfperFrame = res.data.dict.model_dict.main_self.selfperFrame[i]
        this.FuncTop30[i].totalsum = res.data.dict.model_dict.main_self.totalsum[i]
        this.FuncTop30[i].totalperframe = res.data.dict.model_dict.main_self.totalperFrame[i]
      }

    },
    // 请求后端获取CPU主要数据
    async getCPU(){
      const value={
        id:this.id,
        type:3,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.testimglist = res.data.dict.img;
      window.localStorage.setItem("imglist",JSON.stringify(this.testimglist));

      this.CPUSummary.frameIdx = res.data.dict.category_mainThread_frame.frameIdx;
      window.localStorage.setItem("frameIdx",JSON.stringify(this.CPUSummary.frameIdx));
      this.CPUSummary.frameTime = res.data.dict.category_mainThread_frame.frameTime;
      this.CPUSummary.renderingTime = res.data.dict.category_mainThread_frame.Render;
      this.CPUSummary.scriptTime = res.data.dict.category_mainThread_frame.Scripts;
      this.CPUSummary.physicsTime = res.data.dict.category_mainThread_frame.Physics;
      setTimeout(()=>{
        this.DoEchart()
      },500)
    },
    //请求图片信息
    setimglist(){
      window.localStorage.setItem("imglist",JSON.stringify(this.testimglist));
    },

    HandleData(){
      this.testData1.length=this.MainCard.length
      this.testData1[0]=this.SummarytData.fps.toFixed(2)
      this.testData1[1]=this.SummarytData.max_reserved_mono.toFixed(2)
      this.testData1[2]=this.SummarytData.max_texture.toFixed(2)
      this.testData1[3]=this.SummarytData.max_mesh.toFixed(2)
      this.testData1[4]=this.SummarytData.max_animation.toFixed(2)
      this.testData1[5]=this.SummarytData.max_audio.toFixed(2)
      this.testData1[6]=this.SummarytData.max_draw_call
      this.testData1[7]=this.SummarytData.max_tris
    },
    select_color(){
      for(let i=0;i<this.MainCard.length;i++){
        this.MainCard[i].data = this.testData1[i]
        if(i==0){
          if(this.MainCard[i].data>this.MainCard[i].sugdata){
            this.MainCard[i].istrue = true
          }else{
            this.MainCard[i].istrue = false
          }
        }else{
          if(this.MainCard[i].data<this.MainCard[i].sugdata){
            this.MainCard[i].istrue = true
          }else{
            this.MainCard[i].istrue = false
          }
        }
      }
      //console.log(this.MainCard)
    },
    //绘制表
    DoEchart(){
      var ElementID = document.getElementById("CPU_table")
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var data1=[],data2=[],data3=[],data4=[]
      for(let i=0;i<this.CPUSummary.frameIdx.length;i++){
        data4.push(this.CPUSummary.frameTime[i].toFixed(2))
        data1.push(this.CPUSummary.renderingTime[i].toFixed(2))
        data2.push(this.CPUSummary.scriptTime[i].toFixed(2))
        data3.push(this.CPUSummary.physicsTime[i].toFixed(2))
      }
      var seriesData = []
      seriesData[0] = {
        name: "frameTime",
        type: 'line',
        data:data4 ,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
        },
      }
      seriesData[1] = {
        name: "renderingTime",
        type: 'line',
        data: data1,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
        },
      }
      seriesData[2] = {
        name: "scriptTime",
        type: 'line',
        data: data2,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
        },
      }
      seriesData[3] = {
        name: "physicsTime",
        type: 'line',
        data:data3,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
            ]
        },
      }
      let option = {
        title: {
            text: 'CPU时间(ms)',
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"    
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: ['frameTime', 'renderingTime','scriptTime','physicsTime']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.CPUSummary.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    }
  }
}
</script>


