<template>
    <div style="height:auto;min-height:1000px">
      <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>图形合批</el-breadcrumb-item>
      </el-breadcrumb>
      <div style="display:flex;text-align:center;justify-content:center;margin-top:50px">
        <div v-for="(item,index) in Batch_data" :key="index" style="border-right:1px solid #eee;height:160px;width:15%;float:left;margin:25px 0 0 5%;padding-right:4%">
          <div style="color:grey;font-size:15px">
            {{item.title}}
          </div>
          <div style="font-size:40px;margin-top:20px;color:#494a4e"> {{item.value}}</div>
        </div>
      </div>
      <el-collapse v-model="activeNames1" style="margin-top:40px">
        <el-collapse-item name="1">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>DynamicBatched
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="DynamicBatched"></div>
        </el-collapse-item>
        <el-collapse-item name="2">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>StaticBatched
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="StaticBatched"></div>
        </el-collapse-item>
        <el-collapse-item name="3">
          <div slot="title" class="title">
            <i class="el-icon-s-unfold"></i>Batches
          </div>
          <div style="height:350px;width:auto;margin-top:20px" id="Batches"></div>
        </el-collapse-item>              
      </el-collapse>
    </div>     
</template>
<script>
export default {
  data(){
return {  
      Batch_data:[{title:"DrawCalls帧均值(次)",value:null},{title:'DynamicBatches帧均值(次)',value:null},{title:'StaticBatches帧均值(次)',value:null}],
      activeNames1:["1","2","3",],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素  
      AllData:{
        dynamicBatchedDrawCalls:[],
        dynamicBatchedTriangles:[],
        dynamicBatchedVertices:[],
        dynamicBatches:[],

        staticBatchedDrawCalls:[],
        staticBatchedTriangles:[],
        staticBatchedVertices:[],
        staticBatches:[],

        instancedBatches:[],
        setPassCalls:[],
        totalbatches:[],
        drawCalls:[],
        triangles:[],
        vboTotal:[],
        vboTotalBytes:[],
        vertices:[],

        frameIdx:[],

      },
      }
  },
  created(){
    this.getRendering();

  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    async getRendering(){
      const value={
        id:window.localStorage.getItem("id"),
        type:0,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.AllData.dynamicBatchedDrawCalls=res.data.dict.rendering.dynamicBatchedDrawCalls;
      this.AllData.dynamicBatchedTriangles=res.data.dict.rendering.dynamicBatchedTriangles;
      this.AllData.dynamicBatchedVertices=res.data.dict.rendering.dynamicBatchedVertices;
      this.AllData.dynamicBatches=res.data.dict.rendering.dynamicBatches;
      this.AllData.staticBatchedDrawCalls=res.data.dict.rendering.staticBatchedDrawCalls;
      this.AllData.staticBatchedTriangles=res.data.dict.rendering.staticBatchedTriangles;
      this.AllData.staticBatchedVertices=res.data.dict.rendering.staticBatchedVertices;
      this.AllData.staticBatches=res.data.dict.rendering.staticBatches;

      this.AllData.setPassCalls=res.data.dict.rendering.setPassCalls;
      this.AllData.totalbatches=res.data.dict.rendering.totalbatches;
      this.AllData.drawCalls=res.data.dict.rendering.drawCalls;
      this.AllData.triangles=res.data.dict.rendering.triangles;
      this.AllData.vboTotal=res.data.dict.rendering.vboTotal;
      this.AllData.vboTotalBytes=res.data.dict.rendering.vboTotalBytes;
      this.AllData.vertices=res.data.dict.rendering.vertices;
      this.AllData.instancedBatches=res.data.dict.rendering.instancedBatches;

      this.AllData.frameIdx=res.data.dict.rendering.frameIdx;

      this.HandleData();
    },
    HandleData(){
      //console.log((eval(this.AllData.drawCalls.join("+"))/(this.AllData.drawCalls.length)).toFixed(2))
      this.Batch_data[0].value=((eval(this.AllData.drawCalls.join("+"))-Math.max.apply(null,this.AllData.drawCalls)-Math.min.apply(null,this.AllData.drawCalls))/(this.AllData.drawCalls.length-2)).toFixed(2)
      this.Batch_data[1].value=((eval(this.AllData.dynamicBatches.join("+"))-Math.max.apply(null,this.AllData.dynamicBatches)-Math.min.apply(null,this.AllData.dynamicBatches))/(this.AllData.dynamicBatches.length-2)).toFixed(2)
      this.Batch_data[2].value=((eval(this.AllData.staticBatches.join("+"))-Math.max.apply(null,this.AllData.staticBatches)-Math.min.apply(null,this.AllData.staticBatches))/(this.AllData.staticBatches.length-2)).toFixed(2)
      setTimeout(()=>{
        this.DoEchart('DynamicBatched');
        this.DoEchart('StaticBatched');
        this.DoEchart('Batches');
      },100)
    },
    // 折线图
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var seriesData = []
      {
        if(key == 'DynamicBatched'){
          seriesData[0] = {
            name: 'drawcalls',
            type: 'line',
            data: this.AllData.drawCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[1] = {
            name: 'dynamicBatchedDrawCalls',
            type: 'line',
            data: this.AllData.dynamicBatchedDrawCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[2] = {
            name: 'dynamicBatches',
            type: 'line',
            data: this.AllData.dynamicBatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
        }
        if(key == 'StaticBatched'){
          seriesData[0] = {
            name: 'drawcalls',
            type: 'line',
            data: this.AllData.drawCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[1] = {
            name: 'staticBatchedDrawCalls',
            type: 'line',
            data: this.AllData.staticBatchedDrawCalls,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[2] = {
            name: 'staticBatches',
            type: 'line',
            data: this.AllData.staticBatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
        }
        if(key == 'Batches'){
          seriesData[0] = {
            name: 'totalbatches',
            type: 'line',
            data: this.AllData.totalbatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[1] = {
            name: 'dynamicBatches',
            type: 'line',
            data: this.AllData.dynamicBatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[2] = {
            name: 'staticBatches',
            type: 'line',
            data: this.AllData.staticBatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          },
          seriesData[3] = {
            name: 'instancedBatches',
            type: 'line',
            data: this.AllData.instancedBatches,
            markPoint: {
                data: [
                    {type: 'max', name: '最大值'},
                    {type: 'min', name: '最小值'}
                ]
            },
          }
        }
      }
      
      let option = {
        title: {
            text: '次数',
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
             confine:true,
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"      
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: ['drawcalls','totalbatches','dynamicBatchedDrawCalls','dynamicBatches','staticBatchedDrawCalls','staticBatches','instancedBatches']
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.AllData.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    },
  }
}
</script>


<style  lang="less" scoped>
  .el-main {
    background-color:snow;
    width: 100%;
  }
  .el-aside {
	background-color: #333744;
  }
  .el-menu {
      border-right: none;
  }
  .imgHeader {
			width: 170px;
			margin-left: -18px;
			margin-top: 0px;
      float: left;
  }
  .toggle-button {
	background-color: #4a5064;
	font-size: 10px;
	line-height: 20px;
	text-align: center;
	color: white;
	cursor: pointer;
}
  .el-icon-s-unfold{
    color:#1341d8;
    margin-right:10px;
  }
