<template>
  <div style="height:auto;min-height:1000px">
    <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>资源细节</el-breadcrumb-item>
      </el-breadcrumb> 
      <el-tabs v-model="activeNames" @tab-click="clickTabs" stretch style="justify-content:center;margin-top:50px">
        <el-tab-pane  v-for="(item,index) in tabs" :key="index"  :label="item.title"  :name="String(index)">
          <div style="display:flex;text-align:center">
            <div v-for="(item1,index1) in item.content" :key="index1" style="border-right:1px solid #eee;height:160px;width:15%;float:left;margin:25px 0 0 5%;padding-right:4%">
              <div style="color:grey;font-size:15px">
                {{item1.title}}
              </div>
              <div style="font-size:40px;margin-top:20px;color:#494a4e"> {{item1.data}}</div>
            </div>
          </div>
          <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> {{item.id}} size</div>
              <div style="height:350px;width:auto;margin-top:20px" :id="item.id+' Size(MB)'"></div>
          </div>
          <div style="width:100%;float:left;margin-bottom:60px">
            <div style="font-weight:700">
             <i class="el-icon-s-unfold" >
              </i> {{item.id}} Count</div>    
           <div style="height:350px;width:auto;margin-top:20px" :id="item.id+' Count(个)'"></div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
</template>
<script>
export default {
  data(){
return {  
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素 
      activeNames:"0", 
      Alldata:{
        frameIdx:[],
        animationClipsCount:[],
        animationClipsBytes:[],
        audioBytes:[],
        audioCount:[],
        materialBytes:[],
        materialCount:[],
        meshBytes:[],
        meshCount:[],
        textureBytes:[],
        textureCount:[],
      },
      tabs:[{
        id:'Textures',
        title:'纹理资源',
        content:[{title:'TexturesCount峰值(个)',data:null},{title:'TexturesCount帧均值(个)',data:null},{title:'TexturesSize峰值(MB)',data:null},{title:'TexturesSize帧均值(MB)',data:null}]
         },
        {
        id:"Meshes",
        title:'网格资源',
        content:[{title:'MeshesCount峰值(个)',data:null},{title:'MeshesCount帧均值(个)',data:null},{title:'MeshesSize峰值(MB)',data:null},{title:'MeshesSize帧均值(MB)',data:null}],
        },
        {id:"AnimationClips",
        title:'动画资源',
        content:[{title:'AnimationClipsCount峰值(个)',data:null},{title:'AnimationClipsCount帧均值(个)',data:null},{title:'AnimationClipsSize峰值(MB)',data:null},{title:'AnimationClipsSize帧均值(MB)',data:null}],
        },
        {id:"AudioClips",title:'音频资源',
        content:[{title:'AudioClipsCount峰值(个)',data:null},{title:'AudioClipsCount帧均值(个)',data:null},{title:'AudioClipsSize峰值(MB)',data:null},{title:'AudioClipsSize帧均值(MB)',data:null}],
        data:''},
        {id:"Materials",title:'材质资源',
        content:[{title:'MaterialsCount峰值(个)',data:null},{title:'MaterialsCount帧均值(个)',data:null},{title:'MaterialsSize峰值(MB)',data:null},{title:'MaterialsSize帧均值(MB)',data:null}],
        }]
      }
  },
  created(){
    this.getMemory();
    setTimeout(()=>{
      this.DoEchart(this.tabs[0].id+' Size(MB)',1);
      this.DoEchart(this.tabs[0].id+' Count(个)',2);
    },1000)
  },
  computed:{
  },
  methods:{
    async getMemory(){
      const value={
        id:window.localStorage.getItem("id"),
        type:1,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.Alldata.animationClipsCount=res.data.dict.memory.animationClipsCount;
      this.Alldata.animationClipsBytes=res.data.dict.memory.animationClipsBytes;
      this.Alldata.audioBytes=res.data.dict.memory.audioBytes;
      this.Alldata.audioCount=res.data.dict.memory.audioCount;
      this.Alldata.materialBytes=res.data.dict.memory.materialBytes;
      this.Alldata.materialCount=res.data.dict.memory.materialCount;
      this.Alldata.meshBytes=res.data.dict.memory.meshBytes;
      this.Alldata.meshCount=res.data.dict.memory.meshCount;
      this.Alldata.textureBytes=res.data.dict.memory.textureBytes;
      this.Alldata.textureCount=res.data.dict.memory.textureCount;
  
      this.Alldata.frameIdx=res.data.dict.memory.frameIdx;

      this.handleData();
    },
    handleData(){
      this.tabs[0].content[0].data = Math.max.apply(null,this.Alldata.textureCount)  
      this.tabs[0].content[1].data = ((eval(this.Alldata.textureCount.join("+"))   -  Math.max.apply(null,this.Alldata.textureCount)  -Math.min.apply(null,this.Alldata.textureCount)  ) / (this.Alldata.textureCount.length-2)  ).toFixed(0)
      this.tabs[0].content[2].data = (Math.max.apply(null,this.Alldata.textureBytes)/1024/1024).toFixed(2)  
      //this.tabs[0].content[3].data = (eval(this.Alldata.textureBytes.join("+"))/(this.Alldata.textureBytes.length)/1024/1024).toFixed(2)
      this.tabs[0].content[3].data = ((eval(this.Alldata.textureBytes.join("+"))-Math.max.apply(null,this.Alldata.textureBytes)-Math.min.apply(null,this.Alldata.textureBytes))/(this.Alldata.textureCount.length-2)/1024/1024).toFixed(2)
      
      this.tabs[1].content[0].data = Math.max.apply(null,this.Alldata.meshCount)  
      this.tabs[1].content[1].data = ((eval(this.Alldata.meshCount.join("+"))-Math.max.apply(null,this.Alldata.meshCount)-Math.min.apply(null,this.Alldata.meshCount)) / (this.Alldata.meshCount.length-2)).toFixed(0)
      this.tabs[1].content[2].data = (Math.max.apply(null,this.Alldata.meshBytes)/1024/1024).toFixed(2)  
      this.tabs[1].content[3].data = ((eval(this.Alldata.meshBytes.join("+"))-Math.max.apply(null,this.Alldata.meshBytes)-Math.min.apply(null,this.Alldata.meshBytes))/(this.Alldata.meshBytes.length-2)/1024/1024).toFixed(2)

      this.tabs[2].content[0].data = Math.max.apply(null,this.Alldata.animationClipsCount)  
      this.tabs[2].content[1].data = ((eval(this.Alldata.animationClipsCount.join("+"))-Math.max.apply(null,this.Alldata.animationClipsCount)-Math.min.apply(null,this.Alldata.animationClipsCount))/(this.Alldata.textureCount.length-2)).toFixed(0)
      this.tabs[2].content[2].data = (Math.max.apply(null,this.Alldata.animationClipsBytes)/1024/1024).toFixed(2)  
      this.tabs[2].content[3].data = ((eval(this.Alldata.animationClipsBytes.join("+"))-Math.max.apply(null,this.Alldata.animationClipsBytes)-Math.min.apply(null,this.Alldata.animationClipsBytes))/(this.Alldata.animationClipsBytes.length-2)/1024/1024).toFixed(2)

      this.tabs[3].content[0].data = Math.max.apply(null,this.Alldata.audioCount)  
      this.tabs[3].content[1].data = ((eval(this.Alldata.audioCount.join("+"))-Math.max.apply(null,this.Alldata.audioCount)-Math.min.apply(null,this.Alldata.audioCount))/(this.Alldata.audioCount.length-2)).toFixed(0)
      this.tabs[3].content[2].data = (Math.max.apply(null,this.Alldata.audioBytes)/1024/1024).toFixed(2)  
      this.tabs[3].content[3].data = ((eval(this.Alldata.audioBytes.join("+"))-Math.max.apply(null,this.Alldata.audioBytes)-Math.min.apply(null,this.Alldata.audioBytes))/(this.Alldata.audioBytes.length-2)/1024/1024).toFixed(2)

      this.tabs[4].content[0].data = Math.max.apply(null,this.Alldata.materialCount)  
      this.tabs[4].content[1].data = ((eval(this.Alldata.materialCount.join("+"))-Math.max.apply(null,this.Alldata.materialCount)-Math.min.apply(null,this.Alldata.materialCount))/(this.Alldata.materialCount.length-2)).toFixed(0)
      this.tabs[4].content[2].data = (Math.max.apply(null,this.Alldata.materialBytes)/1024/1024).toFixed(2)  
      this.tabs[4].content[3].data = ((eval(this.Alldata.materialBytes.join("+"))-Math.max.apply(null,this.Alldata.materialBytes)-Math.min.apply(null,this.Alldata.materialBytes))/(this.Alldata.materialBytes.length-2)/1024/1024).toFixed(2)

    },

    clickTabs(a){
      setTimeout(()=>{
      this.DoEchart(this.tabs[Number(a.index)].id+' Size(MB)',1);
      this.DoEchart(this.tabs[Number(a.index)].id+' Count(个)',2);
      },100)
    },
    //绘制折线
    DoEchart(key,num){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      var seriesData = [],dataList=[]
      var ledendData=[]
      {
        if(key==this.tabs[0].id+' Size(MB)'){
          ledendData[0] = this.tabs[0].id+' Size'
          for(let i=0;i<this.Alldata.textureBytes.length;i++){
            dataList.push((this.Alldata.textureBytes[i]/1021/1024).toFixed(2))
          }
          seriesData[0] = {
            name: this.tabs[0].id+' Size',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[0].id+' Count(个)'){
          ledendData[0] = this.tabs[0].id+' count'
          dataList = this.Alldata.textureCount
          seriesData[0] = {
            name: this.tabs[0].id+' count',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[1].id+' Size(MB)'){
          ledendData[0] = this.tabs[1].id+' Size'
          for(let i=0;i<this.Alldata.textureBytes.length;i++){
            dataList.push((this.Alldata.meshBytes[i]/1021/1024).toFixed(2))
          }
          seriesData[0] = {
            name: this.tabs[1].id+' Size',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[1].id+' Count(个)'){
          ledendData[0] = this.tabs[1].id+' count'
          dataList = this.Alldata.meshCount
          seriesData[0] = {
            name: this.tabs[1].id+' count',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[2].id+' Size(MB)'){
          ledendData[0] = this.tabs[2].id+' Size'
          for(let i=0;i<this.Alldata.animationClipsBytes.length;i++){
            dataList.push((this.Alldata.animationClipsBytes[i]/1021/1024).toFixed(2))
          }
          seriesData[0] = {
            name: this.tabs[2].id+' Size',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[2].id+' Count(个)'){
          ledendData[0] = this.tabs[2].id+' count'
          dataList = this.Alldata.animationClipsCount
          seriesData[0] = {
            name: this.tabs[2].id+' count',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[3].id+' Size(MB)'){
          ledendData[0] = this.tabs[3].id+' Size'
          for(let i=0;i<this.Alldata.audioBytes.length;i++){
            dataList.push((this.Alldata.audioBytes[i]/1021/1024).toFixed(2))
          }
          seriesData[0] = {
            name: this.tabs[3].id+' Size',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[3].id+' Count(个)'){
          ledendData[0] = this.tabs[3].id+' count'
          dataList = this.Alldata.audioCount
          seriesData[0] = {
            name: this.tabs[3].id+' count',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[4].id+' Size(MB)'){
          ledendData[0] = this.tabs[4].id+' Size'
          for(let i=0;i<this.Alldata.materialBytes.length;i++){
            dataList.push((this.Alldata.materialBytes[i]/1021/1024).toFixed(2))
          }
          seriesData[0] = {
            name: this.tabs[4].id+' Size',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }
        if(key==this.tabs[4].id+' Count(个)'){
          ledendData[0] = this.tabs[4].id+' count'
          dataList = this.Alldata.materialCount
          seriesData[0] = {
            name: this.tabs[4].id+' count',
            type: 'line',
            data: dataList,
            markPoint: {
              data: [
                  {type: 'max', name: '最大值'},
                  {type: 'min', name: '最小值'}
              ]
            },
          }
        }

      }
    
      var Echarttitle=''
      if(num==1){
         Echarttitle = "内存占用总量(MB)"
      }
      if(num==2){
         Echarttitle = "数量(个)"
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      let option = {
        title: {
            text: Echarttitle,
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        tooltip: {
            trigger: 'axis',
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"      
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data:ledendData
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data: this.Alldata.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series: seriesData
      };
       this.BrokenLine.setOption(option)
    },    
  }
}
</script>


<style  lang="less" scoped>
  .el-main {
    background-color:snow;
    width: 100%;
  }
  .el-aside {
	background-color: #333744;
  }
  .el-menu {
      border-right: none;
  }
  .imgHeader {
			width: 170px;
			margin-left: -18px;
			margin-top: 0px;
      float: left;
  }
  .toggle-button {
	background-color: #4a5064;
	font-size: 10px;
	line-height: 20px;
	text-align: center;
	color: white;
	cursor: pointer;
}
  /deep/ .el-tabs__item{
    font-size: 16px ;
  }
    .el-icon-s-unfold{
    color:#1341d8
  }
