<template>
    <div >
    <!-- 面包屑导航区 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Ad_Summary' }">测试概况</el-breadcrumb-item>
        <el-breadcrumb-item>兼容性分析</el-breadcrumb-item>
      </el-breadcrumb> 
      <div style="height:auto;min-height:1000px;">
        <!-- <div>不兼容终端分布</div>
        <div>问题设备数量：</div> -->
        <div v-if="update" style="margin-top:40px;">
          <div style="display:flex">
            <div class="iconfont icon-banben" style="width:45%;margin-bottom:5%;font-weight:700;font-size:25px">系统版本
              <el-table :data="VersionData"  stripe>
                <el-table-column prop="version" label="版本" align="center"></el-table-column>
                <el-table-column prop="devices" label="总台数" align="center"></el-table-column>
                <el-table-column prop="devices_errorNum" label="未通过数" align="center"></el-table-column>
              </el-table>
            </div>
            <div class="iconfont icon-anzhuo- " style="width:45%;margin-left:5%;font-weight:700;font-size:25px">品牌
              <el-table :data="BrandData"  stripe>
                <el-table-column prop="BrandData" label="品牌" align="center"></el-table-column>
                <el-table-column prop="devices" label="总台数" align="center"></el-table-column>
                <el-table-column prop="devices_errorNum" label="未通过数" align="center"></el-table-column>
              </el-table>
            </div>
          </div>
          <div style="display:flex">
            <div class="iconfont icon-RectangleCopy" style="width:45%;margin-bottom:5%;font-weight:700;font-size:25px">内存
              <el-table :data="RamData" stripe>
                <el-table-column prop="RamData" label="内存" align="center"></el-table-column>
                <el-table-column prop="devices" label="总台数" align="center"></el-table-column>
                <el-table-column prop="devices_errorNum" label="未通过数" align="center"></el-table-column>
              </el-table>
            </div>
            <div class="iconfont icon-xingnengtongji " style="width:45%;margin-left:5%;font-weight:700;font-size:25px">分辨率
              <el-table :data="Resolution" stripe>
                <el-table-column prop="Resolution" label="分辨率" align="center" ></el-table-column>
                <el-table-column prop="devices" label="总台数" align="center"></el-table-column>
                <el-table-column prop="devices_errorNum" label="未通过数" align="center"></el-table-column>
              </el-table>
            </div>
          </div>

        </div>
      </div>
    </div>
 
</template>
<script>
export default {
  data(){
    return {  
      update:true,
      VersionData:[{
        version:null,
        devices:null,
        devices_errorNum:null
      },],
      BrandData:[{
        BrandData:null,
        devices:null,
        devices_errorNum:null
      }],
      RamData:[{
        RamData:null,
        devices:null,
        devices_errorNum:null
      }],
      Resolution:[{
        Resolution:null,
        devices:null,
        devices_errorNum:null
      }]
    }

  },
  created(){
    this.getAnalyseInfo();
  },
  computed:{
  },
  methods:{
    isupdate(){
      this.update=false
      this.$nextTick(()=>{
        this.update = true
      })
    },
    getdata(res){
      for(let i=0;i<6;i++){
        this.VersionData[i] = {
          version:null,
          devices:null,
          devices_errorNum:null
        }
        this.VersionData[i].version = res.data.dict.res.system_version.system_version[i]
        this.VersionData[i].devices = res.data.dict.res.system_version.DevicesNum[i]
        this.VersionData[i].devices_errorNum = res.data.dict.res.system_version.Devices_error[i]

        this.BrandData[i]={
          BrandData:null,
          devices:null,
          devices_errorNum:null
        }
        this.BrandData[i].BrandData = res.data.dict.res.brand.brand[i]
        this.BrandData[i].devices = res.data.dict.res.brand.DevicesNum[i]
        this.BrandData[i].devices_errorNum = res.data.dict.res.brand.Devices_error[i]

        this.RamData[i]={
          RamData:null,
          devices:null,
          devices_errorNum:null
        }
        this.RamData[i].RamData = res.data.dict.res.total_memory.total_memory[i]
        this.RamData[i].devices = res.data.dict.res.total_memory.DevicesNum[i]
        this.RamData[i].devices_errorNum = res.data.dict.res.total_memory.Devices_error[i]

        this.Resolution[i]={
          Resolution:null,
          devices:null,
          devices_errorNum:null
        }
        this.Resolution[i].Resolution = res.data.dict.res.resolution.resolution[i]
        this.Resolution[i].devices = res.data.dict.res.resolution.DevicesNum[i]
        this.Resolution[i].devices_errorNum = res.data.dict.res.resolution.Devices_error[i]
      }
      this.isupdate()
    },
    async getAnalyseInfo(){
      let id = {
        id:window.localStorage.getItem('AptReport_id')
      }
      const {data:res} = await this.$http.get('analyse_adaptive_report',{params:id})
      if (res.meta.status !== 200) {
				return this.$message.error(res.meta.msg);
      }
      this.getdata(res);
      
    }
  }
}
</script>


