           <el-table ref="reportTable" :data="pageInfo.currentData" style="width: 100%;cursor:pointer" highlight-current-row  @row-click="open_Unity_detail" >
                <!-- <el-table-column type="selection"></el-table-column> -->
                <el-table-column  label="项目名" width="auto"> 
                  <div class="project">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <img  style="width:52px;height:52px" :src="(ProjectInfo.imgUrl)" />
                      <span style="margin-left:10px ;font-weight:bold;color:blue">{{ProjectInfo.ProjectName}}</span>
                    </div>
                  </div>
                </el-table-column>
                <el-table-column  label="Unity版本" width="auto"> 
                  <div><span>{{ProjectInfo.UnityVersion}}</span></div>
                </el-table-column>
                <el-table-column  label="游戏版本" width="auto"> 
                  <div><span>{{ProjectInfo.ProjectVersion}}</span></div>
                </el-table-column>
                <el-table-column prop="testTime" label="测试时长(min)"  width="auto"></el-table-column>
                <el-table-column prop="testDate" label="测试日期"  width="auto"></el-table-column>
                <el-table-column prop="testDevice" label="测试机型"  width="auto"></el-table-column>
                <el-table-column prop="deviceCpu" label="CPU频率"  width="auto"></el-table-column>
                <el-table-column prop="deviceMemory" label="内存"  width="auto"></el-table-column>
           </el-table>
            <!-- 页码区-->
            <el-pagination @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="UnityTestinfo.length"
                :page-size="10"
                ></el-pagination>
          </el-card>  
        </el-main>
</template>
<script>
export default {
  data(){
return {  
        //项目列表 
        options: [],
        optionsVersion:[],
        optionData:[],
        // 选中数据
        setSelectInfo:{
          ProjectName:'',
          VersionId:'',
        },
        // 修改项目信息
        setGameData:{},
        // 测试数据
        dialogVisable:false,
        dialogVisable1:false,
        gameType:["a","b","c"],
        pageInfo: {
          // 当前的页数
          pagenum: 1,
          // 当前每页显示多少条数据
          pagesize: 8, 
          total:3,
          currentData:[],
        },
        
        ProjectInfo:{
          imgUrl:"",
          ProjectName:'',
          ProjectVersion:'',
          BundleId:'',
          UnityVersion:''
        },
        UnityTestinfo:[{
          testDate:'',
          testTime:'',
          testDevice:'',
          deviceCPU:'',
          deviceMemory:'',
          id:null
        }]
        
      }

  },
  created(){
    this.getProjectList();
    this.getRecentProject();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
     //重置筛选数据
    reset(){
      this.setSelectInfo.ProjectName="";
      this.setSelectInfo.VersionId="";
      this.optionsVersion=[]
      this.ProjectInfo.ProjectName=""
      this.getProjectList();
      this.getRecentProject();

    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.UnityTestinfo.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    // 请求初始化信息
    async getProjectList(){
      const {data:res} = await this.$http.get("init_Unity_project")
      .catch((error) => {
          console.log('错误信息:'+error)
          }
      );
     
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
      this.options= res.data.dict.ProjectList;
       //console.log(this.options)
    },
    //选择项目or版本
    selectProject(val){
      console.log(val);
      console.log(this.options);
      this.setSelectInfo.VersionId=null;
      this.setSelectInfo.ProjectName=this.options[val].ProjectName;
     // this.getSelectProject();
      this.optionsVersion = this.options[val].versionList;
    },
    selectProject1(){
      //console.log(321)
      this.getSelectProject();
    },
    //提交筛选请求
    async getSelectProject(){
      var id={
        id:this.setSelectInfo.VersionId
      };
      const {data:res} = await this.$http.get("select_Unity_project",{params:id})
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
      this.ProjectInfo = res.data.dict.ProjectInfo
      this.UnityTestinfo = res.data.dict.testInfo
      this.setCurrentPageData();
    },
    //初始获取最近项目
    async getRecentProject(){
      const {data:res} = await this.$http.get("Recent_Unity_project")
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
      this.ProjectInfo = res.data.dict.ProjectInfo
      this.UnityTestinfo = res.data.dict.testInfo
      this.setCurrentPageData();
    },

    // 打开unity详情页
    open_Unity_detail(key){
      this.$router.push({path:'/Unity_detail',query:{
          id:key.id,
          ProjectName:this.ProjectInfo.ProjectName,
          UnityVersion:this.ProjectInfo.UnityVersion
      }})
    }
  }
}
</script>

