<template>
      <div style="height:auto;min-height:1000px">
        <!-- 面包屑导航区 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/Unity_Summary' }">测试概况</el-breadcrumb-item>
          <el-breadcrumb-item>CPU性能占用</el-breadcrumb-item>
        </el-breadcrumb>  
        <div>
          <el-tabs v-model="activeNames2" @tab-click="clickTabs" stretch style="justify-content:center;margin-top:50px">
            <el-tab-pane label="均值" name="1">
              <div style="display:flex;text-align:center">
                <div v-for="(item,index) in avg_data" :key="index" style="border-right:1px solid #eee;height:160px;width:15%;float:left;margin:25px 0 0 5%;padding-right:4%">
                  <div style="color:grey;font-size:15px">
                    {{item.title}}
                  </div>
                  <div style="font-size:40px;margin-top:20px;color:#494a4e">{{item.value}}</div>
                </div>
              </div>
            </el-tab-pane>
            <el-tab-pane label="帧占比" name="2">
              <div style="display:flex;text-align:center;">
                <div v-for="(item,index) in frameRate" :key="index" style="border-right:1px solid #eee;height:160px;width:27%;float:left;margin:25px 0 0 4%;">
                  <div style="color:grey;font-size:15px">
                    {{item.title}}
                  </div>
                  <div style="font-size:40px;margin-top:20px;color:#494a4e">{{(item.value)*100}}%</div>
                </div>
              </div>
            </el-tab-pane>
            <!-- <el-tab-pane label="GC" name="3">
              <div style="display:flex;text-align:center;">
                <div v-for="(item,index) in GC_avg" :key="index" style="border-right:1px solid #eee;height:160px;width:27%;float:left;margin:25px 0 0 4%;">
                  <div style="color:grey;font-size:15px">
                    {{item}}
                  </div>
                  <div style="font-size:40px;margin-top:20px;color:#494a4e">30</div>
                </div>
              </div> 
            </el-tab-pane> -->
          </el-tabs>
        </div>
        <!-- <el-divider></el-divider> -->
        <el-collapse v-model="activeNames1" style="margin-top:40px">
          <el-collapse-item name="1">
            <div slot="title" class="title">
              <i class="el-icon-s-unfold"></i>CPU各模块耗时(ms)
            </div>
            <div style="height:350px;width:auto;margin-top:20px;z-index:2" id="CPU_module_ms"></div>
          </el-collapse-item>
          <!-- <el-collapse-item name="2">
            <div slot="title" class="title">
              <i class="el-icon-s-unfold"></i>CPU各模块GC(kb)
            </div>
            <div style="height:350px;width:auto;margin-top:20px" id="CPU_module_GC"></div>
          </el-collapse-item> -->
          <el-collapse-item name="3">
            <div slot="title" class="title">
              <i class="el-icon-s-unfold"></i>各模块总体耗时占比
            </div>
            <div style="display:flex;justify-content:space-between;width:90%;margin-left:5%">
              <div style="height:200px;width:200px" id="Rendering"></div> 
              <div style="height:200px;width:200px" id="Scripting"></div> 
              <div style="height:200px;width:200px" id="Physics"></div> 
              <div style="height:200px;width:200px" id="Loading"></div> 
              <div style="height:200px;width:200px" id="GUI"></div> 
            </div>
                       
          </el-collapse-item>
        </el-collapse>
      </div>     
</template>
<script>
export default {
  data(){
    return {  
      isCollapse: false,
      // 折叠面板设置
      activeNames1:["1","2","3","4"],
      // tab面板设置
      activeNames2:"1",
      // CPU均值面板
      avg_data:[{
        title:"Frame耗时均值(ms)",
        value:0,
        },{
          title:"Render耗时均值(ms)",
          value:0,
        },{
          title:"Script耗时均值(ms)",
          value:0,
        },{
          title:"Physics耗时均值(ms)",
          value:0,
        },{
          title:"GUI耗时均值(ms)",
          value:0,
      }],
      sumRate:[
        {
          title:"Rendering",
          value:0,
        },{
          title:"Scripting",
          value:0,
        },{
          title:"Physics",
          value:0,
        },{
          title:"Loading",
          value:0,
        },{
          title:"GUI",
          value:0,
      }],
      // 帧占比 
      frameRate:[
        {
          title:">16(ms)帧占比",
          value:0,
        },{
          title:">33(ms)帧占比",
          value:0,
        },{
          title:">50(ms)帧占比",
          value:0,
        }],
      //GC均值
      GC_avg:["Script GC均值(KB)",'Rendering GC均值(KB)','Total GC均值(KB)'],
      color:['#ff7f50','#87cefa','#da70d6','#F00078','#9D9D9D','#FF0000'],
      BrokenLine:null,//实例Echart元素      
      CPUperformace:{
        frameIdx:[],
        frameTime:[],
        Render:[],
        PlayerLoop:[],
        Scripts:[],
        Physics:[],
        Loading:[],
        GUI:[],
        GC:[],
        Overhead:[],
      },

    }     
  },
  created(){
    this.getCPU();

    setTimeout(()=>{
      this.DoEchart("CPU_module_ms");
      this.DoEchart_pie("Rendering");
      this.DoEchart_pie("Scripting");
      this.DoEchart_pie("Physics");
      this.DoEchart_pie("Loading");
      this.DoEchart_pie("GUI");
    },500)
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
    clickTabs(tab){
      console.log(tab);
    },
    async getCPU(){
      const value={
        id:window.localStorage.getItem("id"),
        type:3,
      }
      const{data:res} = await this.$http.get("get_u3d_perf",{
        params:value})
      if(res.meta.status!==200){
        return this.$message.error(res.meta.msg)
      }
      this.CPUperformace.frameIdx = res.data.dict.category_mainThread_frame.frameIdx;
      this.CPUperformace.frameTime = res.data.dict.category_mainThread_frame.frameTime;
      this.CPUperformace.Render = res.data.dict.category_mainThread_frame.Render;
      this.CPUperformace.Scripts = res.data.dict.category_mainThread_frame.Scripts;
      this.CPUperformace.Physics = res.data.dict.category_mainThread_frame.Physics;
      this.CPUperformace.GUI = res.data.dict.category_mainThread_frame.GUI;
      this.CPUperformace.GC = res.data.dict.category_mainThread_frame.GC;
      this.CPUperformace.Overhead = res.data.dict.category_mainThread_frame.Overhead;
      this.CPUperformace.Loading = res.data.dict.category_mainThread_frame.Loading;
      this.CPUperformace.PlayerLoop = res.data.dict.category_mainThread_frame.PlayerLoop;
      this.handledata();
      // setTimeout(()=>{
      //   this.DoEchart()
      // },500)
    },
    //数据处理
    handledata(){
       this.avg_data[0].value = ((eval(this.CPUperformace.frameTime.join("+"))-Math.min.apply(null,this.CPUperformace.frameTime)-Math.max.apply(null,this.CPUperformace.frameTime))/(this.CPUperformace.frameTime.length-2)).toFixed(2)
       //console.log((eval(this.CPUperformace.frameTime.join("+"))/(this.CPUperformace.frameTime.length-2)).toFixed(2))
       this.avg_data[1].value = ((eval(this.CPUperformace.Render.join("+"))-Math.min.apply(null,this.CPUperformace.Render)-Math.max.apply(null,this.CPUperformace.Render))/(this.CPUperformace.frameTime.length-2)).toFixed(2)
       this.avg_data[2].value = ((eval(this.CPUperformace.Scripts.join("+"))-Math.min.apply(null,this.CPUperformace.Scripts)-Math.max.apply(null,this.CPUperformace.Scripts))/(this.CPUperformace.frameTime.length-2)).toFixed(2)
       this.avg_data[3].value = ((eval(this.CPUperformace.Physics.join("+"))-Math.min.apply(null,this.CPUperformace.Physics)-Math.max.apply(null,this.CPUperformace.Physics))/(this.CPUperformace.frameTime.length-2)).toFixed(2)
       this.avg_data[4].value = ((eval(this.CPUperformace.GUI.join("+"))-Math.min.apply(null,this.CPUperformace.GUI)-Math.max.apply(null,this.CPUperformace.GUI))/(this.CPUperformace.frameTime.length-2)).toFixed(2)
       this.sumRate[0].value =  (eval(this.CPUperformace.Render.join("+"))/eval(this.CPUperformace.frameTime.join("+"))).toFixed(4)
       this.sumRate[1].value =  (eval(this.CPUperformace.Scripts.join("+"))/eval(this.CPUperformace.frameTime.join("+"))).toFixed(4)
       this.sumRate[2].value =  (eval(this.CPUperformace.Physics.join("+"))/eval(this.CPUperformace.frameTime.join("+"))).toFixed(4)
       this.sumRate[3].value =  (eval(this.CPUperformace.Loading.join("+"))/eval(this.CPUperformace.frameTime.join("+"))).toFixed(4)
       this.sumRate[4].value =  (eval(this.CPUperformace.GUI.join("+"))/eval(this.CPUperformace.frameTime.join("+"))).toFixed(4)     
       //this.sumRate[4].value = (1-this.sumdata[0].value- this.sumdata[1].value- this.sumdata[2].value- this.sumdata[3].value).toFixed(2)
       var a=0,b=0,c=0
       for(let i=0;i<this.CPUperformace.frameTime.length;i++){
         if(this.CPUperformace.frameTime[i]>16){
           a+=1;
         }
         if(this.CPUperformace.frameTime[i]>33){
           b+=1;
         }
         if(this.CPUperformace.frameTime[i]>50){
           b+=1;
         }
       }
       this.frameRate[0].value = (a/this.CPUperformace.frameTime.length).toFixed(4)
       this.frameRate[1].value = (b/this.CPUperformace.frameTime.length).toFixed(4)
       this.frameRate[2].value = (c/this.CPUperformace.frameTime.length).toFixed(4)

      // console.log(this.sumRate)
    },
    //绘制折线
    DoEchart(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var data1=[],data2=[],data3=[],data4=[],data5=[],data6=[],data7=[],data8=[]
      for(let i=0;i<this.CPUperformace.frameIdx.length;i++){
        data1.push(this.CPUperformace.frameTime[i].toFixed(2))
        data2.push(this.CPUperformace.PlayerLoop[i].toFixed(2))
        data3.push(this.CPUperformace.Scripts[i].toFixed(2))
        data4.push(this.CPUperformace.Physics[i].toFixed(2))
        data5.push(this.CPUperformace.Loading[i].toFixed(2))
        data6.push(this.CPUperformace.GUI[i].toFixed(2))
        data7.push(this.CPUperformace.GC[i].toFixed(2))
        data8.push(this.CPUperformace.Overhead[i].toFixed(2))
      }
      var seriesData = []
      {
        seriesData[0] = {
        name: 'frameTime',
        type: 'line',
        data: data1,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[1] = {
        name: 'Render',
        type: 'line',
        data: data2,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[2] = {
        name: 'Scripts',
        type: 'line',
        data: data3,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[3] = {
        name: 'Physics',
        type: 'line',
        data: data4,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[4] = {
        name: 'Overhead',
        type: 'line',
        data: data5,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[5] = {
        name: 'Loading',
        type: 'line',
        data: data6,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[6] = {
        name: 'GUI',
        type: 'line',
        data: data7,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
       seriesData[7] = {
        name: 'GC',
        type: 'line',
        data: data8,
        markPoint: {
            data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
        
        ]},
       }
      }
      let option = {
        title: {
            text: 'CPU时间(ms)',
            textStyle:{
              fontSize:15,
            }
        },
        color:this.color,
        
        tooltip: {
            trigger: 'axis',
            confine:true,
            showDelay:0,//显示延时
            formatter: function(a) {
             let imglist = JSON.parse(window.localStorage.getItem("imglist"))
             let frameIdx = JSON.parse(window.localStorage.getItem("frameIdx"))
             let n = (frameIdx.length/imglist.length).toFixed(2) //一张图片对应多少帧
             let index= 0
             index = parseInt(a[0].name/n)
             let imgsrc = imglist[index]
            //  let url = 'http://10.0.10.246/icon/img/'
            // var img = "<img style='width:120px;height:120px;' src='http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png'/>";
             var img = "<img style='width:300px;height:200px;'src='"+imgsrc+"'/>"      
             //console.log(imglist)
              var showdata='';
              var frame = ''
              for(var i=0;i<a.length;i++){
                 frame = a[i].name
                var name = a[i].seriesName
                var data = a[i].value
                showdata+=name+":  "+data+"<br>";
              } 
              //console.log(a);
              if(a.length > 0){
                return img+"<br>"+frame+"<br>"+showdata;
              }
            },
        },
        legend: {
            data: ['frameTime', 'Render','Scripts','Physics',"Overhead","Loading","GUI","GC"],
            selected:{'frameTime':true, 'Render':true,'Scripts':true,'Physics':true,"Overhead":false,"Loading":false,"GUI":false,"GC":false}
        },
        toolbox: {
            show: false,
            feature: {
                dataView: {show: true, readOnly: false},
                magicType: {show: true, type: ['line', 'bar']},
                restore: {show: true},
                saveAsImage: {show: true}
            }
        },
        calculable: true,
        xAxis: [
            {
                type: 'category',
                data:this.CPUperformace.frameIdx
            }
        ],
        yAxis: [
            {
                type: 'value'
            }
        ],
        series:seriesData
      };
       this.BrokenLine.setOption(option)
    },
    // 饼图
    DoEchart_pie(key){
      var ElementID = document.getElementById(key)
      if(!ElementID){
        return false;
      }
      this.BrokenLine = this.$echarts.init(ElementID)
      var rate=0
      if(key=='Rendering'){
        rate = (this.sumRate[0].value)*100
      }
      if(key=='Scripting'){
        rate = (this.sumRate[1].value)*100
      }
      if(key=='Physics'){
        rate = (this.sumRate[2].value)*100
      }
      if(key=='Loading'){
        rate = (this.sumRate[3].value)*100
      }
      if(key=='GUI'){
        rate = (this.sumRate[4].value)*100
      }
      let option = {
        title:{
          text:key,
          textStyle:{
              fontSize:15
          }
        },
        color:["#8da2e9",'#c5cce2'],
        series: [
          {
            type: 'pie',
            radius: ['40%', '70%'],
            center:['50%','60%'],
            data: [{
                value: rate, 
                name: '直接访问',
                label:{
                    show:true,
                    formatter:'{d}%'
                }},
                {value: 100-rate,
                },
            ],
            label:{
                show:true,
                position:"center",
                fontSize:"15",
                fontWeight:"bold"
            }
          }
        ]}
      this.BrokenLine.setOption(option)
    }
  }
}
</script>


